
<template>
  <div>
    <div style="display: flex; justify-content: center; align-items: center; gap: 15px; margin-bottom: 5px;">
      <h2></h2>
      <button 
        class="toggle-btn"
        @click="toggleDataSource"
      >
        {{ isUsingLocalData ? '' : '' }}
      </button>
      <button 
        class="toggle-btn"
        @click="toggleFullScreen"
        style="background: #4facfe; color: #fff;"
      >
        {{ isFullScreen ? '' : '' }}
      </button>
    </div>
    <div ref="chart" class="chart-container"></div>
    <div v-if="isFullScreen" class="fullscreen-mask" @click.self="closeFullScreen">
      <div class="fullscreen-header">
        <span></span>
        <button 
          class="toggle-btn"
          @click="closeFullScreen"
          style="margin: 0; background: #ff6b6b; color: #fff;"
        ></button>
      </div>
      <div ref="fullscreenChart" class="fullscreen-chart"></div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, reactive, defineEmits } from 'vue'
import * as echarts from 'echarts'
import axios from 'axios' 

const emit = defineEmits(['register', 'chartClick']) 
const isUsingLocalData = ref(true) 
const chartData = reactive({
  legendData: [], 
  seriesData: []  
})


const isFullScreen = ref(false) 
const fullscreenChart = ref(null) 
let fullscreenCChart = null 

const localData = {
  legendData: ['', '', '', '', '', '', ''],
  seriesData: [
    { value: 8643.01, name: '', itemStyle: { color: '#9FA8DA' } },
    { value: 4326.89, name: '', itemStyle: { color: '#A5D6A7' } },
    { value: 2078.59, name: '', itemStyle: { color: '#80CBC4' } },
    { value: 1281.25, name: '', itemStyle: { color: '#81D4FA' } },
    { value: 1946.05, name: '', itemStyle: { color: '#F48FB1' } },
    { value: 310.29, name: '', itemStyle: { color: '#FFE082' } },
    { value: 64.40, name: '', itemStyle: { color: '#BDBDBD' } }
  ]
}

const chart = ref(null)
let myCChart = null


const getChartOption = (targetData, isFull = false) => {
  
  const legendConfig = isFull 
    ? {
        orient: 'vertical',
        left: '10%', 
        top: 'center',
        data: targetData.legendData,
        textStyle: { fontSize: 25, color: '#fff' },
        itemWidth: 50,
        itemHeight: 50
      }
    : {
        orient: 'vertical',
        left: 5, 
        top: 'center',
        data: targetData.legendData,
        textStyle: { fontSize: 10, color: '#fff' },
        itemWidth: 10,
        itemHeight: 8
      };

  const seriesConfig = isFull
    ? {
        name: '',
        type: 'pie',
        radius: ['40%', '70%'],
        center: ['60%', '50%'], 
        itemStyle: {
          borderRadius: 6,
          borderWidth: 2,
          animationDuration: 1000,
          animationEasing: 'cubicOut',
          animationDelay: (idx) => idx * 100
        },
        
        label: {
          show: true, 
          formatter: '{b} ({d}%)', 
          textStyle: {
            color: '#FFFFFF', 
            fontSize: 25, 
            fontWeight: 'normal' 
          }
        },

        labelLine: {
          show: true,
          lineStyle: {
          
            width: 2 
          },
          length: 30, 
          lineLength: 15, 
          maxTurnAngle: 45 
        },
        emphasis: {
          scale: true,
          scaleSize: 10,
          label: {
            show: true,
            formatter: '{b} ({d}%)',
            fontSize: 30,
            color: '#A5D6A7'
          }
        },
        data: targetData.seriesData
      }
    : {
        name: '',
        type: 'pie', 
        radius: ['45%', '75%'],
        //center: ['55%', '40%'], 
        center: ['55%', '54%'], 
        itemStyle: {
          borderRadius: 6,
          borderWidth: 2,
          animationDuration: 1000,
          animationEasing: 'cubicOut',
          animationDelay: (idx) => idx * 100
        },
         labelLine: {
          show: true,
          lineStyle: {
          
          },
          length: 0.3, 
          lineLength: 2, 
          maxTurnAngle: 45 
        },
        emphasis: {
          scale: true,
          scaleSize: 5,
          label: {
            show: true,
            formatter: '{b} ({d}%)',
            fontSize: 10,
            color: '#A5D6A7'
          }
        },
        data: targetData.seriesData
      };

  return {
    legend: legendConfig,
    series: [seriesConfig],
    tooltip: {
      trigger: 'item', 
      backgroundColor: 'rgba(10, 30, 50, 0.9)',
      borderColor: '#1E90FF',
      borderWidth: 1,
      textStyle: { color: '#fff' },
      formatter: function(params) {
       // const inWan = (params.value / 10000).toFixed(2)
        return `<div style="font-size:${isFull ? 19 : 12}px">
                  <strong>${params.name}</strong><br/>
                  <span style="color:#e74c3c">${params.value.toLocaleString()}</span><br/>

                  <span style="color:#2ecc71">${params.percent.toFixed(2)}%</span>
                </div>`
      }
    }
  }
}


const fetchApiData = async () => {
  myCChart?.showLoading() 
  try {
    const res = await axios.get('/three/data')
    const apiData = res.data.data.chartThree.chartData 
    return {
      legendData: apiData.map(item => item.name), 
      seriesData: apiData 
    }
  } catch (error) {
    console.error('', error)
    alert('')
    return localData 
  } finally {
    myCChart?.hideLoading() 
  }
}

const initOrUpdateChart = (targetData) => {
  chartData.legendData = targetData.legendData
  chartData.seriesData = targetData.seriesData

  if (!myCChart && chart.value) {
    myCChart = echarts.init(chart.value)
    // 
    myCChart.on('click', (params) => {
      emit('chartClick', {
        name: params.name,
        value: params.value,
        percent: params.percent
      })
    })
    window.addEventListener('resize', () => {
      myCChart?.resize()
      fullscreenCChart?.resize() 
    })
    // 
    emit('register', myCChart)
  }

  // 
  const option = getChartOption(targetData)
  myCChart.setOption(option)

  // 
  if (isFullScreen.value && fullscreenCChart) {
    const fullOption = getChartOption(targetData, true)
    fullscreenCChart.setOption(fullOption)
  }
}

const initFullScreenChart = () => {
  if (!fullscreenChart.value) return
  fullscreenCChart = echarts.init(fullscreenChart.value)
  // 
  const currentData = isUsingLocalData.value ? localData : {
    legendData: chartData.legendData,
    seriesData: chartData.seriesData
  }
  const option = getChartOption(currentData, true)
  fullscreenCChart.setOption(option)
  fullscreenCChart.resize()
}


const toggleFullScreen = () => {
  isFullScreen.value = !isFullScreen.value
  if (isFullScreen.value) {
    // DOM
    setTimeout(() => initFullScreenChart(), 100)
  } else {
    // 
    if (fullscreenCChart) {
      fullscreenCChart.dispose()
      fullscreenCChart = null
    }
  }
}

// 
const closeFullScreen = () => {
  isFullScreen.value = false
  if (fullscreenCChart) {
    fullscreenCChart.dispose()
    fullscreenCChart = null
  }
}

const toggleDataSource = async () => {
  isUsingLocalData.value = !isUsingLocalData.value
  const targetData = isUsingLocalData.value 
    ? localData 
    : await fetchApiData()
  initOrUpdateChart(targetData)
}

onMounted(() => {
  initOrUpdateChart(localData)
})


</script>

<style scoped>
.chart-container {
  width: 100%;
  height: 250px;
  margin-top: 5px; 
}
h2 {
  height: 0.6rem;
  color: #fff;
  line-height: 0.6rem;
  font-size: 0.25rem;
  text-align: center;
  margin: 0;
  padding-bottom: 5px; 
}
.toggle-btn {
  margin-left: 10px;
  padding: 4px 12px;
  font-size: 0.16rem;
  color: #08bfe0;
  background:#0378df; 
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.toggle-btn:hover {
  background: #0069bf;
}

/*  */
.fullscreen-mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.95);
  z-index: 9999;
  display: flex;
  flex-direction: column;
  padding: 20px;
  box-sizing: border-box;
}
.fullscreen-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  color: #fff;
  font-size: 30px;
}
.fullscreen-chart {
  flex: 1;
  width: 100%;
  border-radius: 6px;
}
</style>