<template>
  <div>
    <!--  -->
    <div style="display: flex; justify-content: center; align-items: center; gap: 15px; margin-bottom: 5px;">
      <h2></h2>
      <!-- / -->
      <button 
        class="chart-type-btn"
        @click="toggleChartType"
      >
        {{ chartType === 'radar' ? '' : '' }}
      </button>
      <!-- / -->
      <button 
        class="fullscreen-btn"
        @click="toggleFullScreen"
      >
        {{ isFullScreen ? '' : '' }}
      </button>
    </div>
    
    <!--  -->
    <div ref="chart" class="chart-container" style="cursor: pointer;"></div>

    <!--  -->
    <div v-if="isFullScreen" class="fullscreen-mask" @click.self="closeFullScreen">
      <div class="fullscreen-header">
        <span></span>
        <!--  -->
        <button 
          class="chart-type-btn"
          @click="toggleChartType"
          style="margin-right: 10px;"
        >
          {{ chartType === 'radar' ? '' : '' }}
        </button>
        <button class="fullscreen-btn close" @click="closeFullScreen"></button>
      </div>
      <div ref="fullscreenChart" class="fullscreen-chart" style="cursor: pointer;"></div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, defineEmits } from 'vue' 
import * as echarts from 'echarts'

const emit = defineEmits(['register']) 
const chart = ref(null)
let myCChart = null // 

// =====================  =====================
const isFullScreen = ref(false) // 
const fullscreenChart = ref(null) // ref
let fullscreenCChart = null // 
// ============================================================

// =====================  =====================
const chartType = ref('radar') // radar-line-
// 
const chartData = {
  categories: ['0-18', '19-35', '36-65', '65', ''],
  series: [
    { name: '', data: [501, 384, 1357, 672, 2914], color: '#FF9AA2' },
    { name: '', data: [643, 600, 2263, 1484, 4990], color: '#7FDBDA' },
    { name: '', data: [311, 208, 1105, 1099, 2723], color: '#B5B9FF' }
  ]
}
// ==============================================================

// 
const LINYI_MEDICAL_URL = 'https://lyyb.linyi.gov.cn/bsfw.htm'

// /
const getChartOption = (isFull = false) => {
  // 
  const fontSize = isFull ? 25 : 12;
  const legendFontSize = isFull ? 25 : 12;
  const symbolSize = isFull ? 10 : 9;
  const lineWidth = isFull ? 3 : 2.5;
  const emphasisLineWidth = isFull ? 4 : 3.5;
  const shadowBlur = isFull ? 8 : 6;
  const emphasisShadowBlur = isFull ? 12 : 10;

  // =====================  =====================
  const radarOption = {
    legend: {
      data: ['', '', ''],
      right: isFull ? '8%' : '5%',
      top: isFull ? '5%' : '8%', 
      orient: 'vertical',
      textStyle: { 
        color: '#ffffff',
        fontSize: legendFontSize,
        fontFamily: 'Microsoft YaHei, sans-serif'
      },
      itemWidth: isFull ? 14 : 12,
      itemHeight: isFull ? 14 : 12,
      itemGap: isFull ? 20 : 15,
      icon: 'circle'
    },
    radar: {
      center: ['45%', '45%'], 
      radius: isFull ? '70%' : '65%',
      shape: 'polygon',
      splitNumber: 4,
      indicator: [
        { name: '0-18', max: 5000 },
        { name: '19-35', max: 5000 },
        { name: '36-65', max: 5000 },
        { name: '65', max: 5000 },
        { name: '', max: 5000 },
      ],
      axisLine: { 
        show: true,
        lineStyle: { 
          color: 'rgba(255,255,255,0.25)',
          width: isFull ? 1.5 : 1,
          type: 'solid'
        } 
      },
      splitLine: { 
        lineStyle: { 
          color: 'rgba(255,255,255,0.5)',
          width: isFull ? 1.5 : 1,
          type: 'solid'
        } 
      },
      splitArea: {
        show: true,
        areaStyle: { 
          color: [
            'rgba(255,255,255,0.03)',
            'rgba(255,255,255,0.01)',
            'rgba(255,255,255,0.03)',
            'rgba(255,255,255,0.01)'
          ]
        }
      },
      name: {
        textStyle: {
          color: '#ffffff', 
          fontSize: fontSize,
          fontWeight: 'normal',
          backgroundColor: 'rgba(0,0,0,0.2)',
          padding: isFull ? [6, 10] : [4, 8],
          borderRadius: 6,
          borderWidth: 1,
          borderColor: 'rgba(255,255,255,0.1)'
        }
      }
    },
    series: [
      {
        name: '',
        type: 'radar',
        smooth: 0.3,
        symbol: 'circle',
        symbolSize: isFull ? 10 : 8,
        lineStyle: {
          width: lineWidth,
          opacity: 0.9
        },
        
        data: [
          {
            value: [501, 384, 1357, 672, 2914],
            name: '',
            lineStyle: { 
              color: '#FF9AA2', 
              width: lineWidth,
             // shadowBlur: shadowBlur,
              //shadowColor: 'rgba(255, 154, 162, 0.5)'
            },
            areaStyle: { 
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                { offset: 0, color: 'rgba(255, 154, 162, 0.3)' },
                { offset: 1, color: 'rgba(255, 154, 162, 0.05)' }
              ])
            },
            itemStyle: { 
              color: '#FFFFFF',
              borderColor: '#FF9AA2',
              borderWidth: isFull ? 3 : 2,
              shadowBlur: isFull ? 6 : 4,
              // shadowColor: 'rgba(255, 154, 162, 0.3)'
            },
            symbol: 'circle',
            symbolSize: symbolSize
          },
          {
            value: [643, 600, 2263, 1484, 4990],
            name: '',
            lineStyle: { 
              color: '#7FDBDA', 
              width: lineWidth,
              // shadowBlur: shadowBlur,
              // shadowColor: 'rgba(127, 219, 218, 0.5)'
            },
            areaStyle: { 
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                { offset: 0, color: 'rgba(127, 219, 218, 0.3)' },
                { offset: 1, color: 'rgba(127, 219, 218, 0.05)' }
              ])
            },
            itemStyle: { 
              color: '#FFFFFF',
              borderColor: '#7FDBDA',
              borderWidth: isFull ? 3 : 2,
              shadowBlur: isFull ? 6 : 4,
              shadowColor: 'rgba(127, 219, 218, 0.3)'
            },
            symbol: 'circle',
            symbolSize: symbolSize
          },
          {
            value: [311, 208, 1105, 1099, 2723],
            name: '',
            lineStyle: { 
              color: '#B5B9FF', 
              width: lineWidth,
              // shadowBlur: shadowBlur,
              // shadowColor: 'rgba(181, 185, 255, 0.5)'
            },
            areaStyle: { 
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                { offset: 0, color: 'rgba(181, 185, 255, 0.3)' },
                { offset: 1, color: 'rgba(181, 185, 255, 0.05)' }
              ])
            },
            itemStyle: { 
              color: '#FFFFFF',
              borderColor: '#B5B9FF',
              borderWidth: isFull ? 3 : 2,
              shadowBlur: isFull ? 6 : 4,
              shadowColor: 'rgba(181, 185, 255, 0.3)'
            },
            symbol: 'circle',
            symbolSize: symbolSize
          }
        ],
        emphasis: {
          lineStyle: {
            width: emphasisLineWidth,
            shadowBlur: emphasisShadowBlur,
            shadowColor: 'rgba(0, 0, 0, 0.2)'
          },
          itemStyle: {
            shadowBlur: isFull ? 10 : 8,
            shadowColor: 'rgba(0, 0, 0, 0.2)'
          },
          areaStyle: {
            opacity: 0.4
          }
        }
      }
    ],
    tooltip: getTooltipConfig(isFull),
    // 
    animation: true,
    animationDuration: 800,
    animationEasing: 'cubicOut'
  };

  // =====================  =====================
  const lineOption = {
    legend: {
      data: ['', '', ''],
      top: isFull ? '5%' : '8%',
      left: 'center',
      orient: 'horizontal',
      textStyle: { 
        color: '#ffffff',
        fontSize: legendFontSize,
        fontFamily: 'Microsoft YaHei, sans-serif'
      },
      itemWidth: isFull ? 14 : 12,
      itemHeight: isFull ? 14 : 12,
      itemGap: isFull ? 20 : 15,
      icon: 'circle'
    },
    grid: {
      left: isFull ? '8%' : '5%',
      right: isFull ? '8%' : '5%',
      top: isFull ? '15%' : '12%',
      bottom: isFull ? '10%' : '8%',
      containLabel: true
    },
    xAxis: {
      type: 'category',
      data: chartData.categories,
      axisLine: { lineStyle: { color: 'rgba(255,255,255,0.6)' } },
      axisTick: { lineStyle: { color: 'rgba(255,255,255,0.4)' } },
      axisLabel: { color: '#ffffff', fontSize: fontSize, rotate: 0 ,  margin: isFull ? 30 : 5}
    },
    yAxis: {
      type: 'value',
      name: '',
      nameTextStyle: { color: '#ffffff', fontSize: fontSize },
      axisLine: { lineStyle: { color: 'rgba(255,255,255,0.6)' } },
      splitLine: { lineStyle: { color: 'rgba(255,255,255,0.2)' } },
      axisLabel: { color: '#ffffff', fontSize: fontSize, formatter: '{value}' }
    },
    series: chartData.series.map(item => ({
      name: item.name,
      type: 'line',
      smooth: 0.6, // 
      data: item.data,
      lineStyle: {
        color: item.color,
        width: lineWidth,
        // shadowBlur: shadowBlur,
        // shadowColor: `${item.color}80`
      },
      symbol: 'circle',
      symbolSize: symbolSize,
      itemStyle: {
        color: '#FFFFFF',
        borderColor: item.color,
        borderWidth: isFull ? 3 : 2,
        shadowBlur: isFull ? 6 : 4,
        shadowColor: `${item.color}4D`
      },
      emphasis: {
        lineStyle: { width: emphasisLineWidth, shadowBlur: emphasisShadowBlur },
        itemStyle: { shadowBlur: isFull ? 10 : 8 },
        areaStyle: { opacity: 0.5 }
      }
    })),
    tooltip: getTooltipConfig(isFull),
    animation: true,
    animationDuration: 800,
    animationEasing: 'cubicOut'
  };
  // ==============================================================

  // 
  return chartType.value === 'radar' ? radarOption : lineOption;
}

// ===================== Tooltip =====================
const getTooltipConfig = (isFull = false) => {
  const fontSize = isFull ? 18 : 12;
  return {
    trigger: 'axis',
    backgroundColor: 'rgba(30, 30, 40, 0.95)',
    borderColor: 'rgba(255, 255, 255, 0.1)',
    borderWidth: 1,
    textStyle: { color: '#ffffff', fontSize: fontSize + 3 },
    formatter: (params) => {
      const category = params[0].axisValue;
      let html = `<div style="font-weight: 600; font-size: ${fontSize + 2}px; margin-bottom: 6px;">${category}</div>`;
      
      params.forEach(item => {
        const value = item.value;
        const color = item.color;
        const seriesIdx = chartData.series.findIndex(s => s.name === item.seriesName);
        const currIdx = chartData.categories.indexOf(category);
        
        // 1
        let ratio = '';
        if (category !== '') {
          const totalValue = chartData.series[seriesIdx].data[4];
          ratio = `${((value/totalValue)*100).toFixed(1)}%`;
        }
        
        // 2
        let change = '';
        if (currIdx > 0 && category !== '') {
          const prevValue = chartData.series[seriesIdx].data[currIdx - 1];
          const changeVal = value - prevValue;
          const changeRate = ((changeVal / prevValue) * 100).toFixed(1);
          change = ` ${changeVal > 0 ? '' : changeVal < 0 ? '' : ''}${Math.abs(changeVal)}${changeRate}%`;
        }
        
        html += `
          <div style="color: #ffffff; padding-left: 8px; font-size: ${fontSize}px; margin-bottom: 2px;">
            <span style="color: ${color}"></span> ${item.seriesName}<span style="font-weight: 600;">${value}</span>${ratio}${change}
          </div>
        `;
      });
      return html;
    }
  };
};
// =============================================================================

const initChart = () => {
  if (!chart.value) return
  myCChart = echarts.init(chart.value)
  
  // 
  const option = getChartOption(false)
  myCChart.setOption(option)
  
  // 
  myCChart.on('click', function() {
  //  window.location.href = LINYI_MEDICAL_URL;
   // 
    window.open(LINYI_MEDICAL_URL, '_blank');
  });
  
  emit('register', myCChart)
  window.addEventListener('resize', () => {
    if (myCChart) {
      myCChart.resize()
    }
    // resize
    if (fullscreenCChart) {
      fullscreenCChart.resize()
    }
  })
}

// =====================  =====================
// 
const initFullScreenChart = () => {
  if (!fullscreenChart.value) return
  fullscreenCChart = echarts.init(fullscreenChart.value)
  
  // 
  const option = getChartOption(true)
  fullscreenCChart.setOption(option)
  
  // 
  fullscreenCChart.on('click', function() {
    window.location.href = LINYI_MEDICAL_URL;
    // 
    // window.open(LINYI_MEDICAL_URL, '_blank');
  });
  
  fullscreenCChart.resize()
}

// /
const toggleFullScreen = () => {
  isFullScreen.value = !isFullScreen.value;
  if (isFullScreen.value) {
    setTimeout(() => initFullScreenChart(), 100); // DOM
  } else {
    closeFullScreen()
  }
}

// 
const closeFullScreen = () => {
  isFullScreen.value = false;
  if (fullscreenCChart) {
    fullscreenCChart.dispose(); // 
    fullscreenCChart = null;
  }
}
// ============================================================

// =====================  =====================
const toggleChartType = () => {
  // 
  chartType.value = chartType.value === 'radar' ? 'line' : 'radar';
  
  // 
  if (myCChart) {
    myCChart.dispose();
    initChart();
  }
  
  // 
  if (isFullScreen.value && fullscreenCChart) {
    fullscreenCChart.dispose();
    initFullScreenChart();
  }
};
// ==============================================================

onMounted(() => {
  initChart()
})
</script>

<style scoped>
/*  */
.chart-container {
  width: 100%;
  height: 280px;
  margin-top: 5px; 
  border-radius: 6px;
  padding: 10px;
}
h2 {
  height: 0.6rem;
  color: #ffffff; 
  line-height: 0.6rem;
  font-size: 0.25rem;
  text-align: center;
  margin: 0;
  padding: 0 0 5px 0;
}

/* =====================  ===================== */
/*  */
.fullscreen-btn {
  padding: 4px 12px;
  font-size: 0.16rem;
  color: #fff;
  background: #4facfe;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.fullscreen-btn:hover {
  background: #00b8ff;
}
.fullscreen-btn.close {
  background: #FF9AA2;
  margin: 0;
}
.fullscreen-btn.close:hover {
  background: #FF6B6B;
}

/*  */
.fullscreen-mask { 
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 10, 20, 0.98);
  z-index: 9999;
  display: flex;
  flex-direction: column;
  padding: 20px;
  box-sizing: border-box;
}

/*  */
.fullscreen-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  color: #ffffff;
  font-size: 35px;
  font-weight: 600;
  text-shadow: 0 0 0px rgba(127, 219, 218, 0.5);
}

/*  */
.fullscreen-chart {
  flex: 1;
  width: 100%;
  border-radius: 6px;
  padding: 20px;
}
/* ============================================================ */

/* =====================  ===================== */
.chart-type-btn {
  padding: 4px 12px;
  font-size: 0.16rem;
  color: #fff;
  background: #025e97; /*  */
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.chart-type-btn:hover {
  background: #034574;
}
/* ============================================================== */

/*  */
@media (max-width: 768px) {
  .chart-container {
    height: 300px;
  }
  .fullscreen-header {
    font-size: 18px;
    flex-wrap: wrap;
    gap: 10px;
  }
}
</style>