<template>
  <div class="app-container">
    <aside class="sidebar" :class="{ collapsed: isSidebarCollapsed }">
      <div class="sidebar-header">
        <h3 v-show="!isSidebarCollapsed"></h3>
        <button class="collapse-toggle" @click="toggleSidebar">
          {{ isSidebarCollapsed ? '>' : '<' }}
        </button>
      </div>
      <nav class="nav-list">
        <div class="nav-item" :class="{ active: activeView === 'all' }" @click="switchView('all')">
          <span class="nav-icon"></span>
          <span class="nav-text" v-show="!isSidebarCollapsed"></span>
        </div>
        <div class="nav-item" :class="{ active: activeView === 'group1' }" @click="switchView('group1')">
          <span class="nav-icon"></span>
          <span class="nav-text" v-show="!isSidebarCollapsed"></span>
        </div>
        <div class="nav-item" :class="{ active: activeView === 'group2' }" @click="switchView('group2')">
          <span class="nav-icon"></span>
          <span class="nav-text" v-show="!isSidebarCollapsed"></span>
        </div>
        <div class="nav-item" :class="{ active: activeView === 'group3' }" @click="switchView('group3')">
          <span class="nav-icon"></span>
          <span class="nav-text" v-show="!isSidebarCollapsed"></span>
        </div>
        <!-- <div class="nav-item" :class="{ active: activeView === 'group4' }" @click="switchView('group4')">
          <span class="nav-icon"></span>
          <span class="nav-text" v-show="!isSidebarCollapsed"></span>
        </div> -->
      </nav>
    </aside>

    <main class="main-content">
      <header>
        <h1>2016-2018</h1>
        <div class="dashboard-controls">
          <button @click="resetLinkage"></button>
          <button @click="simulateDataUpdate"></button>
          <button @click="toggleLinkage">{{ linkageEnabled ? '' : '' }}</button>
          <span class="status">: {{ getViewName }}</span>
          <span class="status">: {{ chartInstances.length }}</span>
          <span class="status">: {{ lastInteraction }}</span>
        </div>
      </header>

      <section class="container">
        <div class="view-all" v-show="activeView === 'all'">
          <section class="itemLeft">
            <ItemPage><ItemOne @register="registerChart" /></ItemPage>
            <ItemPage><ItemTwo @register="registerChart" /></ItemPage>
            <ItemPage><ItemThree @register="registerChart" /></ItemPage>
          </section>
          <section class="itemCenter">
            <MapPage @register="registerChart" />
            <div class="interaction-log">
              <h3></h3>
              <div class="log-content">
                <div v-for="(log, index) in interactionLogs" :key="index" class="log-item">{{ log }}</div>
              </div>
            </div>
          </section>
          <section class="itemRight">
            <ItemPage><ItemFour @register="registerChart" /></ItemPage>
            <ItemPage><ItemFive @register="registerChart" /></ItemPage>
            <ItemPage><ItemSix @register="registerChart" /></ItemPage>
          </section>
        </div>

        <div class="view-group double-chart" v-show="activeView === 'group1' || activeView === 'group2' || activeView === 'group3'">
          <section class="grid-item single-chart" style="flex: 1; width: 100%;">
            <div v-if="activeView === 'group1'" class="group-chart-container" style="width: 100%; height: 100%;">
              <ItemPage><ItemOne @register="registerChart" /></ItemPage>
            </div>
            <div v-if="activeView === 'group2'" class="group-chart-container" style="width: 100%; height: 100%;">
              <ItemPage><ItemFour @register="registerChart" /></ItemPage>
            </div>
            <div v-if="activeView === 'group3'" class="group-chart-container" style="width: 100%; height: 100%;">
              <ItemPage><ItemTwo @register="registerChart" /></ItemPage>
            </div>
          </section>
          <section class="grid-item single-chart" style="flex: 1; width: 100%;" :class="{ 
            'group3-right-chart': activeView === 'group3',
            'group1-right-chart': activeView === 'group1'
          }">
            <div v-if="activeView === 'group1'" class="group-chart-container" style="width: 100%; height: 100%;">
              <ItemPage><ItemThree @register="registerChart" /></ItemPage>
            </div>
            <div v-if="activeView === 'group2'" class="group-chart-container" style="width: 100%; height: 100%;">
              <ItemPage><ItemFive @register="registerChart" /></ItemPage>
            </div>
            <div v-if="activeView === 'group3'" class="group-chart-container group3-right-inner" style="width: 100%; height: 100%;">
              <ItemPage><ItemSix @register="registerChart" /></ItemPage>
            </div>
          </section>
        </div>

        <!-- <div class="view-group fullscreen-chart" v-show="activeView === 'group4'">
          <section class="fullscreen-container single-chart">
            <MapPage @register="registerChart" />
          </section>
        </div> -->
      </section>
    </main>
  </div>
</template>

<script>
import ItemPage from "@/components/itemPage.vue";
import ItemOne from "@/components/itemOne.vue";
import ItemTwo from "@/components/itemTwo.vue";
import ItemThree from "@/components/itemThree.vue";
import ItemFour from "@/components/itemFour.vue";
import ItemFive from "@/components/itemFive.vue";
import ItemSix from "@/components/itemSix.vue";
import MapPage from "@/components/mapPage.vue";
import { onMounted, onUnmounted, ref, computed, watch, nextTick } from 'vue';
import * as echarts from 'echarts';

export default {
  name: 'HomePage',
  components: {
    ItemPage, ItemOne, ItemTwo, ItemThree, ItemFour, ItemFive, ItemSix, MapPage
  },

  setup() {
    const chartInstances = ref([]);
    const linkageEnabled = ref(true);
    const lastInteraction = ref('');
    const interactionLogs = ref([]);
    let updateTimer = null;

    const isSidebarCollapsed = ref(false);
    const toggleSidebar = () => {
      isSidebarCollapsed.value = !isSidebarCollapsed.value;
      addInteractionLog('', '', isSidebarCollapsed.value ? '' : '');
      if (['group1','group2','group3'].includes(activeView.value)) {
        setTimeout(() => resizeGroupCharts(), 100);
      }
    };

    const activeView = ref('all');
    const switchView = (viewType) => {
      activeView.value = viewType;
      addInteractionLog('', '', `${getViewName.value}`);
      nextTick(() => {
        resizeGroupCharts();
        adjustGroupChartText(); //  group3 
        
        if (viewType === 'group2') {
          setTimeout(() => adjustGroup2ChartText(), 600);
          setTimeout(() => unifyGroup2LeftTextStyle(), 800);
          setTimeout(() => adjustGroup2RightChartText(), 1000);
        }
        
        if (viewType === 'group3') {
          setTimeout(() => {
            fixGroup3RightChart(); // 
     
            ensureGroup3LeftChartFonts();
          }, 500);
        }
        if (viewType === 'group1') {
          setTimeout(() => adjustGroup1RightLegend(), 1000);
          setTimeout(() => unifyGroup1LeftTextStyle(), 1200);
          setTimeout(() => adjustGroup1ChartPositions(), 1400);
        }
      });
      if (viewType === 'all') {
        nextTick(() => {
          setTimeout(() => fixItemTwoRender(), 200);
        });
      }
    };

    const getViewName = computed(() => {
      const viewMap = {
        'all': '',
        'group1': '',
        'group2': '',
        'group3': '',
        //'group4': ''
      };
      return viewMap[activeView.value] || '';
    });

    // group1
    const unifyGroup1LeftTextStyle = () => {
      if (activeView.value !== 'group1') return;
      
      let leftChart = null;
      if (Array.isArray(chartInstances.value) && chartInstances.value.length > 0) {
        chartInstances.value.forEach((chart) => {
          if (!chart || chart.isDisposed()) return;
          const chartDom = chart.getDom();
          if (!chartDom) return;
          
          const isRightChart = chartDom.closest('.group1-right-chart');
          const isGroup1 = chartDom.closest('.view-group.double-chart');
          if (isGroup1 && !isRightChart) {
            leftChart = chart;
          }
        });
      }

      if (!leftChart || leftChart.isDisposed()) return;

      const option = leftChart.getOption();
      option.legend = option.legend ? {
        ...option.legend,
        textStyle: {
          ...option.legend.textStyle,
          color: '#ffffff',    
          fontSize: 24,        
          fontWeight: 'bold',  
          textShadowColor: '#000',
          textShadowBlur: 3,    
          textShadowOffsetX: 0,
          textShadowOffsetY: 0
        }
      } : {
        show: true,
        textStyle: {
          color: '#ffffff',
          fontSize: 24,
          fontWeight: 'bold',
          textShadowColor: '#000',
          textShadowBlur: 3
        }
      };
      
      option.xAxis = Array.isArray(option.xAxis) 
        ? option.xAxis.map(axis => ({
            ...axis,
            
            axisLabel: {
              ...axis.axisLabel,
              color: '#ffffff',    
              fontSize: 22,        
              fontWeight: 'bold',  
              textShadowColor: '#000',
              textShadowBlur: 3
            },
            nameTextStyle: {
              ...axis.nameTextStyle,
              color: '#ffffff',    
              fontSize: 22,        
              fontWeight: 'bold',  
              textShadowColor: '#000',
              textShadowBlur: 3
            },
            axisLine: {
              ...axis.axisLine,
              lineStyle: {
                ...axis.axisLine?.lineStyle,
                color: '#ffffff'
              }
            },
            axisTick: {
              ...axis.axisTick,
              lineStyle: {
                ...axis.axisTick?.lineStyle,
                color: '#ffffff'
              }
            }
          }))
        : option.xAxis ? {
            ...option.xAxis,
            axisLabel: {
              ...option.xAxis.axisLabel,
              color: '#ffffff',    
              fontSize: 22,        
              fontWeight: 'bold',  
              textShadowColor: '#000',
              textShadowBlur: 3
            },
            nameTextStyle: {
              ...option.xAxis.nameTextStyle,
              color: '#ffffff',    
              fontSize: 22,        
              fontWeight: 'bold',  
              textShadowColor: '#000',
              textShadowBlur: 3
            },
            axisLine: {
              ...option.xAxis.axisLine,
              lineStyle: {
                ...option.xAxis.axisLine?.lineStyle,
                color: '#ffffff'
              }
            },
            axisTick: {
              ...option.xAxis.axisTick,
              lineStyle: {
                ...option.xAxis.axisTick?.lineStyle,
                color: '#ffffff'
              }
            }
          } : undefined;
      
      option.yAxis = Array.isArray(option.yAxis)
        ? option.yAxis.map(axis => ({
            ...axis,
            axisLabel: {
              ...axis.axisLabel,
              color: '#ffffff',    
              fontSize: 22,        
              fontWeight: 'bold',  
              textShadowColor: '#000',
              textShadowBlur: 3
            },
            nameTextStyle: {
              ...axis.nameTextStyle,
              color: '#ffffff',    
              fontSize: 22,        
              fontWeight: 'bold',  
              textShadowColor: '#000',
              textShadowBlur: 3
            },
            axisLine: {
              ...axis.axisLine,
              lineStyle: {
                ...axis.axisLine?.lineStyle,
                color: '#ffffff'
              }
            },
            axisTick: {
              ...axis.axisTick,
              lineStyle: {
                ...axis.axisTick?.lineStyle,
                color: '#ffffff'
              }
            },
            splitLine: {
              ...axis.splitLine,
              lineStyle: {
                ...axis.splitLine?.lineStyle,
                color: 'rgba(255, 255, 255, 0.2)'
              }
            }
          }))
        : option.yAxis ? {
            ...option.yAxis,
            axisLabel: {
              ...option.yAxis.axisLabel,
              color: '#ffffff',    
              fontSize: 22,        
              fontWeight: 'bold',  
              textShadowColor: '#000',
              textShadowBlur: 3
            },
            nameTextStyle: {
              ...option.yAxis.nameTextStyle,
              color: '#ffffff',    
              fontSize: 22,        
              fontWeight: 'bold',  
              textShadowColor: '#000',
              textShadowBlur: 3
            },
            axisLine: {
              ...option.yAxis.axisLine,
              lineStyle: {
                ...option.yAxis.axisLine?.lineStyle,
                color: '#ffffff'
              }
            },
            axisTick: {
              ...option.yAxis.axisTick,
              lineStyle: {
                ...option.yAxis.axisTick?.lineStyle,
                color: '#ffffff'
              }
            },
            splitLine: {
              ...option.yAxis.splitLine,
              lineStyle: {
                ...option.yAxis.splitLine?.lineStyle,
                color: 'rgba(255, 255, 255, 0.2)'
              }
            }
          } : undefined;
      
      option.series = option.series?.map(series => ({
        ...series,
        label: series.label ? {
          ...series.label,
          color: '#ffffff',    
          fontSize: 22,        
          fontWeight: 'bold',  
          textShadowColor: '#000',
          textShadowBlur: 3
        } : {
          show: true,
          color: '#ffffff',
          fontSize: 22,
          fontWeight: 'bold',
          textShadowColor: '#000',
          textShadowBlur: 3
        },
        itemStyle: {
          ...series.itemStyle,
          opacity: 0.8
        }
      }));
      
      option.tooltip = {
        ...option.tooltip,
        textStyle: {
          ...option.tooltip?.textStyle,
          color: '#ffffff',
          fontSize: 22,
          fontWeight: 'bold'
        },
        backgroundColor: 'rgba(0, 0, 0, 0.7)',
        borderColor: '#ffffff',
        borderWidth: 1
      };
      
      leftChart.setOption(option, {
        replaceMerge: ['legend', 'xAxis', 'yAxis', 'series', 'tooltip'],
        lazyUpdate: false
      });
      
      setTimeout(() => {
        const chartDom = leftChart.getDom();
        if (!chartDom) return;
        
        const legendTextNodes = chartDom.querySelectorAll('.ec-legend-text');
        legendTextNodes.forEach(node => {
          node.style.color = '#ffffff !important';
          node.style.fontSize = '24px !important';
          node.style.fontWeight = 'bold !important';
          node.style.textShadow = '0 0 3px #000 !important';
          node.style.lineHeight = '1.5 !important';
          node.style.display = 'inline-block !important';
        });
        
        const allTextNodes = chartDom.querySelectorAll(
          '.ec-axis-label, .ec-series-label, .ec-tooltip-text'
        );
        allTextNodes.forEach(node => {
          node.style.color = '#ffffff !important';
          node.style.fontSize = '22px !important';
          node.style.fontWeight = 'bold !important';
          node.style.textShadow = '0 0 3px #000 !important';
          node.style.lineHeight = '1.5 !important';
        });
        
        const axisLines = chartDom.querySelectorAll('.ec-axis-line path, .ec-axis-tick line');
        axisLines.forEach(line => {
          line.style.stroke = '#ffffff !important';
        });
        
        const gridLines = chartDom.querySelectorAll('.ec-split-line path');
        gridLines.forEach(line => {
          line.style.stroke = 'rgba(255, 255, 255, 0.2) !important';
        });
      }, 100);
    };

    //  unifyGroup2LeftTextStyle 
    const unifyGroup2LeftTextStyle = () => {
      if (activeView.value !== 'group2') return;
      
      let leftChart = null;
      if (Array.isArray(chartInstances.value) && chartInstances.value.length > 0) {
        chartInstances.value.forEach((chart) => {
          if (!chart || chart.isDisposed()) return;
          const chartDom = chart.getDom();
          if (!chartDom) return;
          
          // group2ItemFour
          const isGroup2 = chartDom.closest('.view-group.double-chart');
          const isLeftChart = chartDom.closest('.grid-item:first-child');
          if (isGroup2 && isLeftChart) {
            leftChart = chart;
          }
        });
      }

      if (!leftChart || leftChart.isDisposed()) return;

      const option = leftChart.getOption();
      option.legend = option.legend ? {
        ...option.legend,
        top: 'top',        
        bottom: 'auto',   
        left: 'center',   
        orient: 'horizontal',
        padding: [10, 0, 10, 0],
        textStyle: {
          ...option.legend.textStyle,
          color: '#ffffff',    // 
          fontSize: 24,        
          fontWeight: 'bold',  
          textShadowColor: '#000',
          textShadowBlur: 3    
        }
      } : {
        show: true,
        top: 'top',
        left: 'center',
        orient: 'horizontal',
        textStyle: {
          color: '#a78bfa',    
          fontSize: 24,
          fontWeight: 'bold',
          textShadowColor: '#000',
          textShadowBlur: 3
        }
      };
      
     
      option.yAxis = Array.isArray(option.yAxis)
        ? option.yAxis.map((axis, index) => {
          
            if (index === 1 || axis.position === 'right') {
              return {
                ...axis,
                nameGap: 80, 
                axisLabel: {
                  ...axis.axisLabel,
                  color: '#a78bfa',   
                  fontSize: 22,        
                  fontWeight: 'bold',  
                  textShadowColor: '#000',
                  textShadowBlur: 3
                },
                nameTextStyle: {
                  ...axis.nameTextStyle,
                  color: '#a78bfa',    
                  fontSize: 22,        
                  fontWeight: 'bold',  
                  textShadowColor: '#000',
                  textShadowBlur: 3
                },
                axisLine: {
                  ...axis.axisLine,
                  lineStyle: {
                    ...axis.axisLine?.lineStyle,
                    color: '#a78bfa'    
                  }
                },
                axisTick: {
                  ...axis.axisTick,
                  lineStyle: {
                    ...axis.axisTick?.lineStyle,
                    color: '#8A2BE2'   
                  }
                },
                splitLine: {
                  ...axis.splitLine,
                  show: false 
                }
              };
            }
           
            return {
              ...axis,
              nameGap: 80, 
              axisLabel: {
                ...axis.axisLabel,
                color: '#ffffff',    
                fontSize: 22,        
                fontWeight: 'bold',  
                textShadowColor: '#000',
                textShadowBlur: 3
              },
              nameTextStyle: {
                ...axis.nameTextStyle,
                color: '#ffffff',    
                fontSize: 22,        
                fontWeight: 'bold',  
                textShadowColor: '#000',
                textShadowBlur: 3
              },
              axisLine: {
                ...axis.axisLine,
                lineStyle: {
                  ...axis.axisLine?.lineStyle,
                  color: '#ffffff'
                }
              },
              axisTick: {
                ...axis.axisTick,
                lineStyle: {
                  ...axis.axisTick?.lineStyle,
                  color: '#ffffff'
                }
              },
              splitLine: {
                ...axis.splitLine,
                lineStyle: {
                  ...axis.splitLine?.lineStyle,
                  color: 'rgba(255, 255, 255, 0.2)'
                }
              }
            };
          })
        : option.yAxis ? {
           
            ...option.yAxis,
            axisLabel: {
              ...option.yAxis.axisLabel,
              color: option.yAxis.position === 'right' ? '#8A2BE2' : '#ffffff',    
              fontSize: 22,        
              fontWeight: 'bold',  
              textShadowColor: '#000',
              textShadowBlur: 3
            },
            nameTextStyle: {
              ...option.yAxis.nameTextStyle,
              color: option.yAxis.position === 'right' ? '#8A2BE2' : '#ffffff',
              fontSize: 22,        
              fontWeight: 'bold',  
              textShadowColor: '#000',
              textShadowBlur: 3
            },
            axisLine: {
              ...option.yAxis.axisLine,
              lineStyle: {
                ...option.yAxis.axisLine?.lineStyle,
                color: option.yAxis.position === 'right' ? '#8A2BE2' : '#ffffff'
              }
            },
            axisTick: {
              ...option.yAxis.axisTick,
              lineStyle: {
                ...option.yAxis.axisTick?.lineStyle,
                color: option.yAxis.position === 'right' ? '#8A2BE2' : '#ffffff'
              }
            },
            splitLine: option.yAxis.position === 'right' ? 
              { show: false } : 
              {
                ...option.yAxis.splitLine,
                lineStyle: {
                  ...option.yAxis.splitLine?.lineStyle,
                  color: 'rgba(255, 255, 255, 0.2)'
                }
              }
          } : undefined;
      
     
      option.xAxis = Array.isArray(option.xAxis) 
        ? option.xAxis.map(axis => ({
            ...axis,
            axisLabel: {
              ...axis.axisLabel,
              color: '#ffffff',    
              fontSize: 22,        
              fontWeight: 'bold',  
              textShadowColor: '#000',
              textShadowBlur: 3
            },
            nameTextStyle: {
              ...axis.nameTextStyle,
              color: '#ffffff',    
              fontSize: 22,        
              fontWeight: 'bold',  
              textShadowColor: '#000',
              textShadowBlur: 3
            },
            axisLine: {
              ...axis.axisLine,
              lineStyle: {
                ...axis.axisLine?.lineStyle,
                color: '#ffffff'
              }
            },
            axisTick: {
              ...axis.axisTick,
              lineStyle: {
                ...axis.axisTick?.lineStyle,
                color: '#ffffff'
              }
            }
          }))
        : option.xAxis ? {
            ...option.xAxis,
            axisLabel: {
              ...option.xAxis.axisLabel,
              color: '#ffffff',    
              fontSize: 22,        
              fontWeight: 'bold',  
              textShadowColor: '#000',
              textShadowBlur: 3
            },
            nameTextStyle: {
              ...option.xAxis.nameTextStyle,
              color: '#ffffff',    
              fontSize: 22,        
              fontWeight: 'bold',  
              textShadowColor: '#000',
              textShadowBlur: 3
            },
            axisLine: {
              ...option.xAxis.axisLine,
              lineStyle: {
                ...option.xAxis.axisLine?.lineStyle,
                color: '#ffffff'
              }
            },
            axisTick: {
              ...option.xAxis.axisTick,
              lineStyle: {
                ...option.xAxis.axisTick?.lineStyle,
                color: '#ffffff'
              }
            }
          } : undefined;
      
      option.series = option.series?.map(series => ({
        ...series,
        label: series.label ? {
          ...series.label,
          color: '#ffffff',    
          fontSize: 22,        
          fontWeight: 'bold',  
          textShadowColor: '#000',
          textShadowBlur: 3
        } : {
          show: true,
          color: '#ffffff',
          fontSize: 22,
          fontWeight: 'bold',
          textShadowColor: '#000',
          textShadowBlur: 3
        },
        itemStyle: {
          ...series.itemStyle,
          opacity: 0.8
        },
        
        symbolSize: series.symbolSize ? series.symbolSize * 1.2 : 8,
        lineStyle: series.lineStyle ? {
          ...series.lineStyle,
          width: (series.lineStyle.width || 2) * 1.5,
          color: '#8A2BE2'  
        } : {
          width: 3,
          color: '#8A2BE2'  
        }
      }));
      
      option.tooltip = {
        ...option.tooltip,
        textStyle: {
          ...option.tooltip?.textStyle,
          color: '#ffffff',
          fontSize: 22,
          fontWeight: 'bold'
        },
        backgroundColor: 'rgba(0, 0, 0, 0.7)',
        borderColor: '#ffffff',
        borderWidth: 1
      };
      
      leftChart.setOption({ 
        legend: option.legend,
        yAxis: option.yAxis,
        xAxis: option.xAxis,
        series: option.series,
        tooltip: option.tooltip
      }, {
        replaceMerge: ['legend', 'xAxis', 'yAxis', 'series', 'tooltip'],
        lazyUpdate: false
      });
      
      // DOM
      setTimeout(() => {
        const chartDom = leftChart.getDom();
        if (!chartDom) return;
        
        // 
        const legendTextNodes = chartDom.querySelectorAll('.ec-legend-text');
        legendTextNodes.forEach(node => {
          node.style.color = '#8A2BE2 !important';  // 
          node.style.fontSize = '24px !important';
          node.style.fontWeight = 'bold !important';
          node.style.textShadow = '0 0 3px #000 !important';
          node.style.lineHeight = '1.5 !important';
          node.style.display = 'inline-block !important';
        });
        
        // 
        const allTextNodes = chartDom.querySelectorAll(
          '.ec-axis-label, .ec-series-label, .ec-tooltip-text'
        );
        allTextNodes.forEach(node => {
          node.style.color = '#ffffff !important';
          node.style.fontSize = '22px !important';
          node.style.fontWeight = 'bold !important';
          node.style.textShadow = '0 0 3px #000 !important';
          node.style.lineHeight = '1.5 !important';
        });
        
        // 
        const axisLines = chartDom.querySelectorAll('.ec-axis-line path, .ec-axis-tick line');
        axisLines.forEach(line => {
          line.style.stroke = '#ffffff !important';
        });
        
        // 
        const gridLines = chartDom.querySelectorAll('.ec-split-line path');
        gridLines.forEach(line => {
          line.style.stroke = 'rgba(255, 255, 255, 0.2) !important';
        });
      }, 100);
    };
    
    // group1
    const adjustGroup1ChartPositions = () => {
      if (activeView.value !== 'group1') return;
      
      let leftChart = null;
      chartInstances.value.forEach((chart) => {
        if (!chart || chart.isDisposed()) return;
        const chartDom = chart.getDom();
        if (!chartDom) return;
        
        const isRightChart = chartDom.closest('.group1-right-chart');
        const isGroup1 = chartDom.closest('.view-group.double-chart');
        if (isGroup1 && !isRightChart) {
