                  lineStyle: {
                    ...axis.axisTick?.lineStyle,
                    color: '#ffffff'
                  }
                }
              }))
            : option.xAxis ? { 
                ...option.xAxis,
                axisLabel: { 
                  ...option.xAxis.axisLabel,
                  fontSize: 22, 
                  fontWeight: 'bold',
                  color: '#ffffff',    
                  textShadowColor: '#000',
                  textShadowBlur: 3
                }, 
                nameTextStyle: { 
                  ...option.xAxis.nameTextStyle,
                  fontSize: 22, 
                  fontWeight: 'bold',
                  color: '#ffffff',    
                  textShadowColor: '#000',
                  textShadowBlur: 3
                },
                axisLine: {
                  ...option.xAxis.axisLine,
                  lineStyle: {
                    ...option.xAxis.axisLine?.lineStyle,
                    color: '#ffffff'
                  }
                },
                axisTick: {
                  ...option.xAxis.axisTick,
                  lineStyle: {
                    ...option.xAxis.axisTick?.lineStyle,
                    color: '#ffffff'
                  }
                }
              } : undefined,
          // Y
          yAxis: Array.isArray(option.yAxis)
            ? option.yAxis.map(axis => ({
                ...axis,
                axisLabel: { 
                  ...axis.axisLabel,
                  fontSize: 22, 
                  fontWeight: 'bold',
                  color: '#ffffff',    
                  textShadowColor: '#000',
                  textShadowBlur: 3
                },
                nameTextStyle: { 
                  ...axis.nameTextStyle,
                  fontSize: 22, 
                  fontWeight: 'bold',
                  color: '#ffffff',    
                  textShadowColor: '#000',
                  textShadowBlur: 3
                },
                axisLine: {
                  ...axis.axisLine,
                  lineStyle: {
                    ...axis.axisLine?.lineStyle,
                    color: '#ffffff'
                  }
                },
                axisTick: {
                  ...axis.axisTick,
                  lineStyle: {
                    ...axis.axisTick?.lineStyle,
                    color: '#ffffff'
                  }
                },
                splitLine: {
                  ...axis.splitLine,
                  lineStyle: {
                    ...axis.splitLine?.lineStyle,
                    color: 'rgba(255, 255, 255, 0.2)'
                  }
                }
              }))
            : option.yAxis ? { 
                ...option.yAxis,
                axisLabel: { 
                  ...option.yAxis.axisLabel,
                  fontSize: 22, 
                  fontWeight: 'bold',
                  color: '#ffffff',    
                  textShadowColor: '#000',
                  textShadowBlur: 3
                }, 
                nameTextStyle: { 
                  ...option.yAxis.nameTextStyle,
                  fontSize: 22, 
                  fontWeight: 'bold',
                  color: '#ffffff',    
                  textShadowColor: '#000',
                  textShadowBlur: 3
                },
                axisLine: {
                  ...option.yAxis.axisLine,
                  lineStyle: {
                    ...option.yAxis.axisLine?.lineStyle,
                    color: '#ffffff'
                  }
                },
                axisTick: {
                  ...option.yAxis.axisTick,
                  lineStyle: {
                    ...option.yAxis.axisTick?.lineStyle,
                    color: '#ffffff'
                  }
                },
                splitLine: {
                  ...option.yAxis.splitLine,
                  lineStyle: {
                    ...option.yAxis.splitLine?.lineStyle,
                    color: 'rgba(255, 255, 255, 0.2)'
                  }
                }
              } : undefined,
          // 24px
          legend: { 
            ...option.legend,
            textStyle: { 
              ...option.legend?.textStyle,
              fontSize: 24, 
              fontWeight: 'bold',
              color: '#ffffff',    
              textShadowColor: '#000',
              textShadowBlur: 3
            } 
          },
          // 
          tooltip: { 
            ...option.tooltip,
            textStyle: { 
              ...option.tooltip?.textStyle,
              fontSize: 22, 
              fontWeight: 'bold',
              color: '#ffffff'
            },
            backgroundColor: 'rgba(0, 0, 0, 0.7)',
            borderColor: '#ffffff',
            borderWidth: 1
          },
          // 
          series: option.series?.map(series => ({
            ...series,
            label: series.label ? {
              ...series.label,
              fontSize: 22,
              fontWeight: 'bold',
              color: '#ffffff',    
              textShadowColor: '#000',
              textShadowBlur: 3
            } : {
              show: true,
              color: '#ffffff',
              fontSize: 22,
              fontWeight: 'bold',
              textShadowColor: '#000',
              textShadowBlur: 3
            },
            itemStyle: {
              ...series.itemStyle,
              opacity: 0.8
            }
          }))
        };
        
        // 
        chart.setOption(newOption, {
          replaceMerge: ['legend', 'xAxis', 'yAxis', 'series', 'tooltip', 'title'],
          lazyUpdate: false
        });
        
        // DOM
        setTimeout(() => {
          const chartDom = chart.getDom();
          if (!chartDom) return;
          
          // 
          const legendText = chartDom.querySelectorAll('.ec-legend-text');
          legendText.forEach(node => {
            node.style.color = '#ffffff !important';
            node.style.fontSize = '24px !important';
            node.style.fontWeight = 'bold !important';
            node.style.textShadow = '0 0 3px #000 !important';
          });
          
          // /
          const textNodes = chartDom.querySelectorAll('.ec-axis-label, .ec-series-label');
          textNodes.forEach(node => {
            node.style.color = '#ffffff !important';
            node.style.fontSize = '22px !important';
            node.style.fontWeight = 'bold !important';
            node.style.textShadow = '0 0 3px #000 !important';
          });
          
          // 
          const axisLines = chartDom.querySelectorAll('.ec-axis-line path, .ec-axis-tick line');
          axisLines.forEach(line => {
            line.style.stroke = '#ffffff !important';
          });
        }, 100);
      });
    };

    // 
    const fixItemTwoRender = () => {
      if (activeView.value !== 'all') return;
      if (!chartInstances.value || chartInstances.value.length < 2) return;
      const itemTwoChart = chartInstances.value[1];
      if (!itemTwoChart || itemTwoChart.isDisposed()) return;

      const option = itemTwoChart.getOption();
      itemTwoChart.setOption(option, true);
      itemTwoChart.resize({
        width: 'auto',
        height: 'auto',
        silent: false
      });
    };

    // fixGroup3RightChart 
    const fixGroup3RightChart = () => {
      if (activeView.value !== 'group3') return;
      if (!chartInstances.value || chartInstances.value.length < 7) return;
      const pieChart = chartInstances.value[6];
      if (!pieChart || pieChart.isDisposed()) return;

      const option = pieChart.getOption();
      
      // 
      option.series = option.series?.map(series => ({
        ...series,
        radius: ['20%', '75%'],
        center: ['50%', '50%'],
        labelLine: {
          show: true,
          length: 22,
          length2: 15,
          smooth: 0.2
        },
         // ==========  +   ==========
    label: {
      ...series.label, // 
      color: '#ffffff',
      fontSize: 26,    // 
      fontWeight: 'bold',
      textShadowColor: '#000',
      textShadowBlur: 4, 
      padding: [5,9],   // 
      overflow: 'break' // 
    },
     // ==========   ==========
    emphasis: {
      focus: 'descendant',
      label: {
        show: true,
        fontSize: 28,    // ,28/30/32
        fontWeight: 'bold',
        color: '#ffffff',
        textShadowColor: '#000',
        textShadowBlur: 5, 
        padding: [6,10],   
        overflow: 'break'  
      }
    }
    // ==========   ==========
      }));
      

      option.legend = {
        ...option.legend,
        top: '5%', 
        left: 'center',
        textStyle: {
          ...option.legend?.textStyle,
          fontSize: 24, //  24px
          fontWeight: 'bold',
          color: '#ffffff',
          textShadowColor: '#000',
          textShadowBlur: 3
        }
      };
      
      // 
      if (option.series && option.series[0]) {
        option.series[0].label = {
          ...option.series[0].label,
          color: '#ffffff',
          fontSize: 26,
          fontWeight: 'bold',
          textShadowColor: '#000',
          textShadowBlur: 3
        };
      }
      
      pieChart.setOption(option, true);
      
      const pieDom = pieChart.getDom();
      if (!pieDom || !pieDom.parentElement) return;
      pieChart.resize({
        width: pieDom.parentElement.clientWidth,
        height: pieDom.parentElement.clientHeight,
        silent: false
      });
      
      // DOM 
      setTimeout(() => {
        const chartDom = pieChart.getDom();
        if (!chartDom) return;
        
        const allText = chartDom.querySelectorAll('.ec-legend-text, .ec-label');
        allText.forEach(node => {
          node.style.color = '#ffffff !important';
          node.style.fontSize = '24px !important';
          node.style.fontWeight = 'bold !important';
          node.style.textShadow = '0 0 3px #000 !important';
        });
      }, 150);
    };

    const resizeGroupCharts = () => {
      if (!['group1','group2','group3'].includes(activeView.value)) return;
      if (!chartInstances.value || chartInstances.value.length === 0) return;
      
      chartInstances.value.forEach(chart => {
        if (!chart || chart.isDisposed()) return;
        const chartDom = chart.getDom();
        if (!chartDom) return;
        
        const parent = chartDom.closest('.group-chart-container');
        if (!parent) return;
        
        chart.resize({ 
          width: parent.offsetWidth, 
          height: parent.offsetHeight, 
          silent: true 
        });
        chartDom.style.margin = '0 auto';
        chartDom.style.display = 'block';
        chartDom.style.overflow = 'visible';
      });
    };

    const addInteractionLog = (chartName, action, details) => {
      const time = new Date().toLocaleTimeString();
      const log = `[${time}] ${chartName}: ${action} ${details ? `- ${details}` : ''}`;
      interactionLogs.value.unshift(log);
      lastInteraction.value = log;
      if (interactionLogs.value.length > 10) interactionLogs.value.pop();
    };

    const registerChart = (instance) => {
      if (!instance || chartInstances.value.includes(instance)) return;
      chartInstances.value.push(instance);
      
      const chartNames = [
        '', 'TOP10', '', 
        '', '', '', ''
      ];
      const chartIndex = chartInstances.value.length - 1;
      const chartName = chartNames[chartIndex] || `${chartIndex + 1}`;
      
      const chartTypeMap = {
        'TOP10': 'bar',
        '': 'pie',
        '': 'bar',
        '': 'pie',
        '': 'scatter',
        '': 'bar'
      };
      const chartType = chartTypeMap[chartName];
      instance.isBarChart = chartType === 'bar';
      instance.isPieChart = chartType === 'pie';
      instance.isScatterChart = chartType === 'scatter';
      instance.isLinkageChart = ['', ''].includes(chartName);
      
      setupChartEvents(instance, chartName);
      addInteractionLog(chartName, '', '');
      
      if (chartIndex === 1 && activeView.value === 'all') {
        setTimeout(() => fixItemTwoRender(), 300);
      }
      
      if (chartIndex === 2 && activeView.value === 'group1') {
        setTimeout(() => adjustGroup1RightLegend(), 1000);
      }
      
      if (['group1','group2','group3'].includes(activeView.value)) {
        setTimeout(() => resizeGroupCharts(), 300);
        if (chartIndex === 6 && activeView.value === 'group3') {
          setTimeout(() => fixGroup3RightChart(), 500);
        }
      }
    };

    const setupChartEvents = (chart, chartName) => {
      if (!chart) return;

      chart.on('click', (params) => {
        if (!linkageEnabled.value) return;
        addInteractionLog(chartName, '', `${params.name || params.seriesName}`);
        const linkageCharts = chartInstances.value.filter(item => item && !item.isDisposed() && item.isLinkageChart);
        linkageCharts.forEach(otherChart => {
          if (otherChart !== chart && params.dataIndex !== undefined) {
            try {
              otherChart.dispatchAction({ type: 'highlight', seriesIndex: 0, dataIndex: params.dataIndex });
              otherChart.dispatchAction({ type: 'showTip', seriesIndex: 0, dataIndex: params.dataIndex || 0 });
            } catch (error) {
              console.debug('', error, `${chartName}`);
            }
          }
        });
      });

      chart.on('mouseover', (params) => {
        if (!linkageEnabled.value) return;
        const linkageCharts = chartInstances.value.filter(item => item && !item.isDisposed() && item.isLinkageChart);
        linkageCharts.forEach(otherChart => {
          if (otherChart !== chart && params.dataIndex !== undefined) {
            try {
              otherChart.dispatchAction({ type: 'showTip', seriesIndex: 0, dataIndex: params.dataIndex });
            } catch (error) {
              console.debug('', error, `${chartName}`);
            }
          }
        });
      });

      chart.on('globalout', () => {
        if (!linkageEnabled.value) return;
        chartInstances.value.forEach(otherChart => {
          if (!otherChart || otherChart.isDisposed() || otherChart === chart) return;
          try {
            otherChart.dispatchAction({ type: 'hideTip' });
            otherChart.dispatchAction({ type: 'downplay' });
          } catch (error) {
            console.debug('', error, `${chartName}`);
          }
        });
      });

      chart.on('selectchanged', (params) => {
        if (!linkageEnabled.value) return;
        const selectedNames = params.selected.map(item => item.name).join(', ');
        addInteractionLog(chartName, '', selectedNames || '');
      });
    };

    const resetLinkage = () => {
      if (!chartInstances.value || chartInstances.value.length === 0) return;
      
      chartInstances.value.forEach(chart => {
        if (!chart || chart.isDisposed()) return;
        try {
          chart.dispatchAction({ type: 'downplay' });
          chart.dispatchAction({ type: 'hideTip' });
        } catch (error) {
          console.debug('', error);
        }
      });
      addInteractionLog('', '', '');
    };

    const simulateDataUpdate = () => {
      addInteractionLog('', '', '...');
      setTimeout(() => {
        if (!chartInstances.value || chartInstances.value.length === 0) return;
        
        const randomIndex = Math.floor(Math.random() * chartInstances.value.length);
        const chart = chartInstances.value[randomIndex];
        if (!chart || chart.isDisposed()) return;
        
        try {
          const option = chart.getOption();
          if (option.series && option.series[0]) {
            const newData = option.series[0].data.map(item => {
              if (typeof item === 'number') return item * (0.9 + Math.random() * 0.2);
              else if (item && item.value) return { ...item, value: item.value * (0.9 + Math.random() * 0.2) };
              return item;
            });
            chart.setOption({
              series: [{ ...option.series[0], data: newData, animationDuration: 1000, animationEasing: 'cubicOut' }]
            });
            addInteractionLog(`${randomIndex + 1}`, '', '');
          }
        } catch (error) {
          console.log(':', error);
        }
      }, 1500);
    };

    const toggleLinkage = () => {
      linkageEnabled.value = !linkageEnabled.value;
      addInteractionLog('', linkageEnabled.value ? '' : '');
    };

    const startAutoUpdate = () => {
      if (updateTimer) clearInterval(updateTimer);
      updateTimer = setInterval(() => {
        if (chartInstances.value.length > 0 && Math.random() > 0.7) simulateDataUpdate();
      }, 30000);
    };

    const initEchartsConnection = () => {
      if (!chartInstances.value || chartInstances.value.length === 0) return;
      
      try {
        echarts.connect(chartInstances.value);
        const groupId = 'dashboard-charts';
        chartInstances.value.forEach((chart) => { 
          if (chart && !chart.isDisposed()) chart.group = groupId;
        });
        addInteractionLog('', '', `${chartInstances.value.length}`);
        startAutoUpdate();
      } catch (error) {
        console.error(':', error);
      }
    };

    const initHeightAdaptation = () => {
      const handleResize = () => {
        if (['group1','group2','group3'].includes(activeView.value)) {
          resizeGroupCharts();
          if (activeView.value === 'group3') {
            fixGroup3RightChart();
          }
          if (activeView.value === 'group1') {
            adjustGroup1RightLegend();
            unifyGroup1LeftTextStyle();
            adjustGroup1ChartPositions();
          }
          //group2
          if (activeView.value === 'group2') {
            setTimeout(() => adjustGroup2ChartText(), 300);
            setTimeout(() => unifyGroup2LeftTextStyle(), 500);
            setTimeout(() => adjustGroup2RightChartText(), 700);
          }
        }
        if (activeView.value === 'all') {
          fixItemTwoRender();
        }
      };

      const container = document.querySelector('.container');
      if (container) {
        const resizeObserver = new ResizeObserver(handleResize);
        resizeObserver.observe(container);
      }
      window.addEventListener('resize', handleResize);
      onUnmounted(() => window.removeEventListener('resize', handleResize));
    };

    const loadChartsByLayer = () => {
      setTimeout(() => {
        if (!chartInstances.value || chartInstances.value.length < 4) {
          addInteractionLog('', '', '', 'warn');
          initEchartsConnection();
          initHeightAdaptation();
          return;
        }
        
        const mapIndex = 3;
        const mapInstance = chartInstances.value[mapIndex];
        if (mapInstance && !mapInstance.isDisposed()) {
          mapInstance.resize();
          addInteractionLog('', '', '');
          setTimeout(() => {
            [0,1,2].forEach(index => {
              const chart = chartInstances.value[index];
              if (chart && !chart.isDisposed()) {
                chart.resize();
                addInteractionLog(`${index + 1}`, '', '1');
              }
            });
            setTimeout(() => {
              [4,5,6].forEach(index => {
                const chart = chartInstances.value[index];
                if (chart && !chart.isDisposed()) {
                  chart.resize();
                  addInteractionLog(`${index + 1}`, '', '1.5');
                }
              });
              initEchartsConnection();
              initHeightAdaptation();
              addInteractionLog('', '', '');
            }, 500);
          }, 1000);
        } else {
          addInteractionLog('', '', '', 'warn');
          initEchartsConnection();
          initHeightAdaptation();
        }
      }, 500);
    };

    const handleKeyDown = (event) => {
      switch(event.key) {
        case 'r': case 'R': if (event.ctrlKey) resetLinkage(); break;
        case 'l': case 'L': if (event.ctrlKey) toggleLinkage(); break;
        case 'u': case 'U': if (event.ctrlKey) simulateDataUpdate(); break;
        case 'a': case 'A': if (event.ctrlKey) switchView('all'); break;
        case 's': case 'S': if (event.ctrlKey) toggleSidebar(); break;
      }
    };

    onMounted(() => {
      loadChartsByLayer();
      document.addEventListener('keydown', handleKeyDown);
      setTimeout(() => {
        if (activeView.value === 'all') {
          fixItemTwoRender();
        }
      }, 1000);
    });

    onUnmounted(() => {
      if (updateTimer) clearInterval(updateTimer);
      document.removeEventListener('keydown', handleKeyDown);
      if (chartInstances.value && chartInstances.value.length > 0) {
        chartInstances.value.forEach(chart => {
          if (chart && !chart.isDisposed()) {
            try { chart.dispose(); } catch (error) { console.debug('', error); }
          }
        });
        chartInstances.value = [];
      }
    });

    return {
      chartInstances, linkageEnabled, lastInteraction, interactionLogs,
      registerChart, resetLinkage, simulateDataUpdate, toggleLinkage,
      activeView, switchView, getViewName, isSidebarCollapsed, toggleSidebar
    };
  }
};
</script>

<style lang="less">
.app-container {
  display: flex;
  height: 100vh;
  overflow: hidden;
}

.sidebar {
  width: 180px;
  background: rgba(0, 0, 50, 0.8);
  border-right: 1px solid rgba(8, 230, 218, 0.5);
  color: #fff;
  display: flex;
  flex-direction: column;
  transition: width 0.3s ease;
  
  &.collapsed {
    width: 60px;
    .nav-item {
      justify-content: center;
      padding: 8px 0;
      .nav-icon { margin-right: 0; }
    }
  }
  
  .sidebar-header {
    padding: 10px;
    border-bottom: 1px solid rgba(8, 230, 218, 0.3);
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    h3 { margin: 0; font-size: 16px; color: #4ecdc4; text-align: center; }
    
    .collapse-toggle {
      width: 24px; height: 24px; border-radius: 50%;
      background: rgba(8, 230, 218, 0.2); color: #fff;
      border: 1px solid rgba(8, 230, 218, 0.5);
      cursor: pointer; display: flex;
      align-items: center; justify-content: center;
      font-size: 14px; transition: all 0.2s;
      
      &:hover { background: rgba(8, 230, 218, 0.3); }
      &:active { background: rgba(8, 230, 218, 0.4); }
    }
  }
  
  .nav-list {
    padding: 10px 0; flex: 1; overflow-y: auto;
    
    .nav-item {
      display: flex; align-items: center;
      padding: 8px 15px; cursor: pointer;
      transition: all 0.2s;
      font-size: 16px;
      
      &:hover { background: rgba(8, 230, 218, 0.1); }
      &.active {
        background: rgba(8, 230, 218, 0.2);
        border-left: 3px solid #4ecdc4;
        
        .sidebar.collapsed & {
          border-left: none;
          background: rgba(8, 230, 218, 0.3);
          border-radius: 4px; margin: 0 4px;
        }
      }
      
      .nav-icon { margin-right: 10px; width: 20px; text-align: center; }
      .nav-text { font-size: 16px; white-space: nowrap; }
    }
  }
}

.main-content {
  flex: 1; display: flex; flex-direction: column;
  overflow: hidden; transition: all 0.3s ease;
}

header {
  height: 1rem; width: 100%;
  background-color: rgba(0, 0, 255, .2);
  position: relative;
  
  h1 {
    font-size: 24px; color: #fff; text-align: center; line-height: 1rem; margin: 0;
  }
  
  .dashboard-controls {
    position: absolute; top: 1.8px; right: 0.2rem;
    display: flex; align-items: center; gap: 0.1rem;
    
    button {
      padding: 0.03rem 0.08rem;
      background-color: rgba(255, 255, 255, 0.15);
      border: 1px solid rgba(255, 255, 255, 0.25);
      color: white; border-radius: 3px;
      cursor: pointer;
      font-size: 16px;
      transition: background-color 0.2s;
      
      &:hover { background-color: rgba(255, 255, 255, 0.25); }
      &:active { background-color: rgba(255, 255, 255, 0.35); }
    }
    
    .status {
      color: #4ecdc4;
      font-size: 16px;
      margin-left: 0.15rem;
      background: rgba(0, 0, 0, 0.15);
      padding: 0.03rem 0.06rem; border-radius: 2px;
      white-space: nowrap;
    }
  }
}

.container {
  width: 100%;
  height: calc(100vh - 1rem);
  overflow: hidden;
  padding: .125rem;
  box-sizing: border-box;
  
  .view-all {
    display: flex;
    height: 100%;
    
    .itemLeft, .itemRight {
      flex: 4; display: flex; flex-direction: column;
      gap: 0.125rem; border: 1px solid rgba(8, 230, 218, 0.3);
      height: 100%;
      > div { flex: 1; min-height: 180px; height: 100%; }
    }
    
    .itemCenter {
      flex:5; height: 100%; min-height: 400px;
      border: 1px solid rgba(8, 230, 218, 0.3);
      padding: 0.125rem; margin: 3.8px;
      display: flex; flex-direction: column;
      
      > div:first-child { flex: 1; min-height: 250px; height: 100%; }
      
      .interaction-log {
        flex: 0 1 2.5rem; max-height: 300px;
        margin-top: 0.1rem; background: rgba(0, 0, 0, 0.1);
        border-radius: 3px; padding: 0.08rem; overflow: hidden;
        border: 1px solid rgba(8, 230, 218, 0.3);
        
        h3 {
          color: #fff; font-size: 18px;
          margin: 0 0 0.03rem 0; text-align: center;
          border-bottom: 1px solid rgba(255, 255, 255, 0.15);
          padding-bottom: 0.03rem;
        }
        
        .log-content {
          height: calc(100% - 0.25rem); overflow-y: auto;
          padding: 0.03rem;
          
          .log-item {
            color: #a9dfbf;
            font-size: 16px;
            padding: 0.02rem 0.04rem;
            border-bottom: 1px solid rgba(255, 255, 255, 0.05);
            word-break: break-all; line-height: 1.5;
            &:hover { background: rgba(255, 255, 255, 0.05); }
          }
        }
      }
    }
  }

  .view-group.double-chart {
    display: flex !important;
    flex-direction: row;
    gap: 0.2rem;
    height: 100%;
    width: 100%;
    padding: 0.15rem;
    box-sizing: border-box;

    .grid-item {
      border: 2px solid rgba(8, 230, 218, 0.5);
      background: rgba(0, 0, 50, 0.1);
      padding: 0.2rem;
