<template>
  <div>

    <div style="display: flex; justify-content: center; align-items: center; gap: 15px; margin-bottom: 5px;"
         :style="{ fontSize: isFullScreen ? '18px' : 'inherit' }">
      <h2></h2>
      <select 
        v-if="!isFullScreen"
        @change="handleFilterChange"
        :style="{
          padding: '5px 12px',
          border: 'none',
          borderRadius: '4px',
          background: '#2c3e50',
          color: '#fff',
          fontSize: '14px',
          cursor: 'pointer'
        }"
      >
        <option value="highest"> Top10</option>
        <option value="lowest"> Top10</option>
        <option value="middle"> Top10</option>
      </select>
      <button 
        @click="toggleFullScreen" 
        :style="{ 
          padding: isFullScreen ? '8px 16px' : '5px 12px', 
          border: 'none', 
          borderRadius: '4px', 
          background: '#4facfe', 
          color: '#fff', 
          cursor: 'pointer', 
          fontSize: isFullScreen ? '16px' : '12px' 
        }"
      >
        {{ isFullScreen ? '' : '' }}
      </button>
    </div>
    <div ref="chart" class="chart-container" :style="{ display: isFullScreen ? 'none' : 'block' }"></div>
    
    <div v-if="isFullScreen" class="fullscreen-modal" @click.self="closeFullScreen">
     
      <div class="fullscreen-header">
        <span style="font-size: 36px; font-weight: bold;">{{ filterType === 'highest' ? '' : filterType === 'lowest' ? '' : '' }}10</span>
        <button 
          @click="closeFullScreen" 
          :style="{ 
            padding: '8px 16px', 
            border: 'none', 
            background: '#ff6b6b', 
            color: '#fff', 
            borderRadius: '4px', 
            cursor: 'pointer',
            fontSize: '16px' 
          }"
        >
          
        </button>
      </div>
      <div ref="fullscreenChart" class="fullscreen-chart-container"></div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, onUnmounted, watch } from 'vue'
import * as echarts from 'echarts'

const emit = defineEmits(['register']) 
const chart = ref(null)
const fullscreenChart = ref(null)
let myCChart = null 
let fullscreenCChart = null 

const fullscreenCarouselTimer = ref(null)
const fullscreenCurrentIndex = ref(0)
const carouselInterval = 3000
const isFullScreen = ref(false)
// 
const filterType = ref('highest')

// 
const fullData = [
  { name: '', cost: 167191.71 },
  { name: '', cost: 76936.12 },
  { name: '', cost: 59469.24 },
  { name: '', cost: 51511.65 },
  { name: '', cost: 49435.15 },
  { name: '', cost: 49387.07 },
  { name: '', cost: 41103.32 },
  { name: '', cost: 40163.25 },
  { name: '', cost: 39734.05 },
  { name: '', cost: 39691.84 },
  { name: '', cost: 12500.88 },
  { name: '', cost: 8900.56 },
  { name: '', cost: 7800.45 },
  { name: '', cost: 6500.78 },
  { name: '', cost: 5600.99 },
  { name: '', cost: 4800.33 },
  { name: '', cost: 3200.67 },
  { name: '', cost: 2800.89 },
  { name: '', cost: 1500.22 },
  { name: '', cost: 980.55 }
]

//  no-case-declarations 
const getFilteredData = (type) => {
  // 
  const sortedData = [...fullData]
  let result = []
  
  switch(type) {
    case 'highest': { // 
      // Top10
      result = sortedData.sort((a, b) => b.cost - a.cost).slice(0, 10)
      break
    }
    case 'lowest': { // 
      // 10
      result = sortedData.sort((a, b) => a.cost - b.cost).slice(0, 10)
      break
    }
    case 'middle': { // 
      // 1010
      const sorted = sortedData.sort((a, b) => a.cost - b.cost)
      const middleStart = Math.floor((sorted.length - 10) / 2)
      result = sorted.slice(middleStart, middleStart + 10)
      break
    }
    default: { // 
      result = sortedData.sort((a, b) => b.cost - a.cost).slice(0, 10)
      break
    }
  }
  
  return result
}

// 
const getChartOption = (isFull = false) => {
  const filteredData = getFilteredData(filterType.value)
  // 
  const names = filteredData.map(item => item.name)
  const costs = filteredData.map(item => item.cost)
  
  return {
    tooltip: {
      trigger: 'axis',
      formatter: function(params) {
        const rank = params[0].dataIndex + 1;
        const cost = params[0].value.toFixed(2);
        return `
          <div style="text-align: left; padding: 8px;">
            <div style="font-weight: bold; color: #00f5ff; font-size: ${isFull ? '16px' : '14px'};">${params[0].name}</div>
            <div style="font-size: ${isFull ? '25px' : '15px'};">${rank}</div>
            <div style="font-size: ${isFull ? '14px' : '12px'};">${cost}</div>
          </div>
        `;
      },
      backgroundColor: 'rgba(10, 30, 50, 0.9)',
      borderColor: '#1E90FF',
      borderWidth: 1,
      borderRadius: 4,
      textStyle: {
        color: '#fff',
        fontSize: isFull ? 30 : 12
      },
      padding: [10, 15],
      position: 'top',
      confine: true,
      enterable: false
    },
    grid: {
      left: isFull ? '4%' : '3%',
      right: isFull ? '4%' : '8%',
      bottom: isFull ? '5%' : '3%',
      top: isFull ? '10%' : '10%',
      containLabel: true,
      show: true, // grid
      borderColor: 'rgba(255,255,255,0.1)' // 
    },
    xAxis: {
      type: 'value',
      axisLabel: {
        color: '#ffffff',
        fontSize: isFull ? 25 : 12
      },
      axisLine: {
        lineStyle: {
          color: '#666'
        }
      }
    },
    yAxis: {
      type: 'category',
      data: names,
      axisLabel: {
        margin: 20,
        color: '#ffffff',
        fontSize: isFull ? 25 : 12
      },
      axisLine: {
        lineStyle: {
          color: '#666'
        }
      }
    },
    
    series: [{
      type: 'bar',
      data: costs,
      itemStyle: {
        color: '#87CEEB',
        emphasis: {
          color: '#00f5ff',
          shadowBlur: isFull ? 15 : 10,
          shadowColor: 'rgba(0, 245, 255, 0.5)'
        }
      }
    }]
  };
}

// 
const startCarouselFn = (chartInstance, timerRef, indexRef) => {
  // 
  if (!chartInstance) return
  
  if (timerRef.value) clearInterval(timerRef.value)
  
  timerRef.value = setInterval(() => {
    // 
    if (!chartInstance || chartInstance.isDisposed?.()) {
      clearInterval(timerRef.value)
      timerRef.value = null
      return
    }
    
    const dataLen = chartInstance.getOption().yAxis[0].data.length
    chartInstance.dispatchAction({
      type: 'unhighlight',
      seriesIndex: 0,
      dataIndex: indexRef.value
    })
    
    indexRef.value = (indexRef.value + 1) % dataLen
    
    chartInstance.dispatchAction({
      type: 'highlight',
      seriesIndex: 0,
      dataIndex: indexRef.value
    })
    chartInstance.dispatchAction({
      type: 'showTip',
      seriesIndex: 0,
      dataIndex: indexRef.value,
      position: 'top'
    })
  }, carouselInterval)
}

const pauseNormalCarousel = () => {
  // 
  if (myCChart && !myCChart.isDisposed?.()) {
    myCChart.dispatchAction({ type: 'unhighlight', seriesIndex: 0 })
    myCChart.dispatchAction({ type: 'hideTip' })
  }
}

// resize
const safeResize = (chartInstance) => {
  // 
  if (chartInstance && typeof chartInstance.resize === 'function' && !chartInstance.isDisposed?.()) {
    try {
      chartInstance.resize()
    } catch (e) {
      console.warn('Chart resize failed:', e)
    }
  }
}

// 
const updateChart = () => {
  if (myCChart && !myCChart.isDisposed?.()) {
    const option = getChartOption(false)
    myCChart.setOption(option)
    safeResize(myCChart) // resize
  }
  // 
  if (isFullScreen.value && fullscreenCChart && !fullscreenCChart.isDisposed?.()) {
    const option = getChartOption(true)
    fullscreenCChart.setOption(option)
    // 
    startCarouselFn(fullscreenCChart, fullscreenCarouselTimer, fullscreenCurrentIndex)
    safeResize(fullscreenCChart) // resize
  }
}

// 
const handleFilterChange = (e) => {
  filterType.value = e.target.value
  updateChart()
}

const initChart = () => {
  if (!chart.value) return
  
  // 
  if (myCChart) {
    try {
      if (!myCChart.isDisposed?.()) {
        myCChart.dispose()
      }
    } catch (e) {
      console.warn('Dispose old chart failed:', e)
    }
    myCChart = null
  }
  
  myCChart = echarts.init(chart.value)
  const option = getChartOption(false)
  myCChart.setOption(option)
  emit('register', myCChart)
  
  // 
  myCChart.on('mouseover', (params) => {
    pauseNormalCarousel()
  })
  
  window.addEventListener('resize', handleResize)
}

// 
const initFullScreenChart = () => {
  if (!fullscreenChart.value) return
  
  // 
  if (fullscreenCChart) {
    try {
      if (!fullscreenCChart.isDisposed?.()) {
        fullscreenCChart.dispose()
      }
    } catch (e) {
      console.warn('Dispose fullscreen chart failed:', e)
    }
    fullscreenCChart = null
  }
  
  fullscreenCChart = echarts.init(fullscreenChart.value)
  const option = getChartOption(true) // true
  fullscreenCChart.setOption(option)
  
  fullscreenCChart.on('mouseover', (params) => {
    if (fullscreenCarouselTimer.value) {
      clearInterval(fullscreenCarouselTimer.value)
      fullscreenCarouselTimer.value = null
    }
    fullscreenCurrentIndex.value = params.dataIndex
  })
  
  fullscreenCChart.on('mouseout', () => {
    if (fullscreenCChart && !fullscreenCChart.isDisposed?.() && !fullscreenCarouselTimer.value) {
      fullscreenCarouselTimer.value = startCarouselFn(fullscreenCChart, fullscreenCarouselTimer, fullscreenCurrentIndex)
    }
  })
  
  fullscreenCarouselTimer.value = startCarouselFn(fullscreenCChart, fullscreenCarouselTimer, fullscreenCurrentIndex)
  safeResize(fullscreenCChart) // resize
}

const toggleFullScreen = () => {
  isFullScreen.value = !isFullScreen.value
  
  if (isFullScreen.value) {
    pauseNormalCarousel() // 
    setTimeout(() => initFullScreenChart(), 100)
  } else {
    closeFullScreen()
  }
}

// 
const closeFullScreen = () => {
  isFullScreen.value = false
  
  if (fullscreenCarouselTimer.value) {
    clearInterval(fullscreenCarouselTimer.value)
    fullscreenCarouselTimer.value = null
  }
  
  if (fullscreenCChart) {
    try {
      if (!fullscreenCChart.isDisposed?.()) {
        fullscreenCChart.dispatchAction({ type: 'hideTip' })
        fullscreenCChart.dispose()
      }
    } catch (e) {
      console.warn('Dispose fullscreen chart failed:', e)
    }
    fullscreenCChart = null
  }
  
  // resize
  if (myCChart) {
    // 100msDOMresize
    setTimeout(() => {
      safeResize(myCChart) // resize
    }, 100)
  }
}

// resize
const handleResize = () => {
  // 
  if (!isFullScreen.value) {
    safeResize(myCChart)
  }
  safeResize(fullscreenCChart)
}

// 
watch(filterType, () => {
  updateChart()
})

// onMounted(() => {
//   // DOM
//   setTimeout(() => {
//     initChart()
//   }, 0)
// })

//  onMounted 
onMounted(() => {
  console.log('')
  
  // 1 nextTick
  // import { nextTick } from 'vue'
  // nextTick(() => {
  //   setTimeout(initChart, 50)
  // })
  
  // 2
  const initWhenReady = () => {
    if (!chart.value) {
      console.warn('...')
      setTimeout(initWhenReady, 50)
      return
    }
    
    const rect = chart.value.getBoundingClientRect()
    console.log(':', rect)
    
    if (rect.width > 10 && rect.height > 10) {
      console.log('')
      initChart()
    } else {
      console.warn('')
      setTimeout(initWhenReady, 50)
    }
  }
  
  // 
  if (document.readyState === 'loading') {
    window.addEventListener('DOMContentLoaded', initWhenReady)
  } else {
    // 
    setTimeout(initWhenReady, 50)
  }
  
  // resize
  let resizeTimer
  window.addEventListener('resize', () => {
    clearTimeout(resizeTimer)
    resizeTimer = setTimeout(() => {
      if (myCChart && !myCChart.isDisposed?.()) {
        myCChart.resize()
      }
      if (fullscreenCChart && !fullscreenCChart.isDisposed?.()) {
        fullscreenCChart.resize()
      }
    }, 200)
  })
  
  onUnmounted(() => {
    window.removeEventListener('resize', handleResize)
  })
})

onUnmounted(() => {
  pauseNormalCarousel()
  closeFullScreen()
  
  // 
  if (myCChart) {
    try {
      if (!myCChart.isDisposed?.()) {
        myCChart.dispose()
      }
    } catch (e) {
      console.warn('Dispose chart failed:', e)
    }
    myCChart = null
  }
  
  window.removeEventListener('resize', handleResize)
})
</script>

<style scoped>
.chart-container {
  width: 100%;
  height: 240px;
  margin-top: 10px;
  border-radius: 4px;
}
h2 {
  height: .6rem;
  color: #fff;
  line-height: .6rem;
  font-size: 0.25rem;
  text-align: center;
  margin: 0;
  /*  */
  font-size: clamp(16px, 0.25rem, 24px);
}

.fullscreen-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.95);
  z-index: 9999;
  display: flex;
  flex-direction: column;
  padding: 20px;
  box-sizing: border-box;
  overflow: hidden;
}

.fullscreen-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  color: #fff;
   font-size: 25px; /* 30px/35px */
  /*  */
}

.fullscreen-chart-container {
  flex: 1;
  width: 100%;
  height: calc(100% - 40px);
  min-height: 300px;
  border-radius: 6px;
}
</style>