<template>
  <div>
    <!--  -->
    <div style="display: flex; justify-content: center; align-items: center; gap: 15px; margin-bottom: 5px;">
      <h2></h2>
      <!--  -->
      <button 
        class="fullscreen-btn"
        @click="toggleFullScreen"
      >
        {{ isFullScreen ? '' : '' }}
      </button>
    </div>
    
    <!--  -->
    <div ref="chart" class="chart-container"></div>

    <!--  -->
    <div v-if="isFullScreen" class="fullscreen-mask" @click.self="closeFullScreen">
      <div class="fullscreen-header">
        <span></span>
        <button class="fullscreen-btn close" @click="closeFullScreen"></button>
      </div>
      <div ref="fullscreenChart" class="fullscreen-chart"></div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, defineEmits } from 'vue'
import * as echarts from 'echarts'

const emit = defineEmits(['register']) 
const chart = ref(null)
let myCChart = null 
const isFullScreen = ref(false) 
const fullscreenChart = ref(null) 
let fullscreenCChart = null 

// 
const getAgeCostAverage = () => {
  const ageGroups = {
    : [0, 18],    
    : [19, 35],   
    : [36, 55],   
    : [56, 95]    
  }
  const allData = [
    ...[[12, 4539.06], [6, 4259.71], [6, 4502.23], [2, 11703.17], [3, 18338.08], [13, 1429.69], [2, 3690.94],[3, 398.37],[5, 18417.99],[15, 1813.88]],
    ...[[29,13792.46],[32, 1670.55], [31, 2546.01], [32, 3533.38],[33, 2266.45],[34, 1036.73],[21, 167191.17],[22, 873.43], [34, 286.83], [20, 1532.77]],
    ...[[42, 8129.72],[45, 1805.82], [49, 1522.71],[38, 13290.86],[50, 1696.96],[49, 1281.19],[43, 2495.31],[45, 4549.96],[49, 7260.63],[50, 7273.57]],
    ...[[91, 6641.21],[85, 588.31],[66, 14986.27],[66, 1098.47],[69, 365.24],[71, 74.89], [86, 103.36],[78, 369.37],[88, 564.94],[77, 252.19]]
  ]

  const averageData = []
  Object.entries(ageGroups).forEach(([group, [minAge, maxAge]]) => {
    const groupData = allData.filter(([age]) => age >= minAge && age <= maxAge)
    if (groupData.length === 0) return
    const totalCost = groupData.reduce((sum, [_, cost]) => sum + cost, 0)
    const avgCost = totalCost / groupData.length
    const midAge = (minAge + maxAge) / 2 
    averageData.push([midAge, Math.round(avgCost)])
  })
  return averageData
}

// 
const getChartOption = (isFull = false) => {
  const fontSize = isFull ? 25 : 12
  const symbolSize = isFull ? 25 : 14
  const emphasisSize = isFull ? 25 : 20
  const averageData = getAgeCostAverage()
  
  // 
  const mainColor = '#8b5cf6' // 
  const axisTextColor = '#a78bfa' // 
  const axisLineColor = '#c4b5fd' // 

  return {
    legend: {
      data: ['', '', '', '', ''],
      top: isFull ? 20 : 0, 
      textStyle: { color: '#ffffff', fontSize }, 
      itemWidth: isFull ? 14 : 12,
      itemHeight: isFull ? 14 : 12,
      icon: 'circle'
    },
    grid: {
      left: isFull ? '12%' : '10%',
      right: isFull ? '12%' : '15%', 
      bottom: isFull ? '15%' : '20%',
      top: isFull ? '15%' : '10%', 
      containLabel: true
    },
    xAxis: {
      name: '',
      nameTextStyle: { color: '#ffffff', fontSize, fontWeight: 'bold' },
      nameLocation: 'bottom',
    
      nameGap: isFull ? 100 : 30, 
      type: 'value',
      min: 0,
      max: 90,
      axisLine: { lineStyle: { color: '#888888', width: 1.5 } }, 
      axisLabel: { color: '#ffffff', fontSize }, 
      splitLine: { lineStyle: { color: 'rgba(255,255,255,0.2)' } } 
    },
    // 
    yAxis: [
      {
        name: '',
        nameTextStyle: { color: '#ffffff', fontSize, fontWeight: 'bold' },
        nameLocation: 'middle',
        nameGap: isFull ? 100 : 40,
        type: 'value',
        min: 0,
        max: 20000,
        axisLine: { lineStyle: { color: '#888888', width: 1.5 } }, 
        axisLabel: { 
          color: '#ffffff', fontSize,
          formatter: value => value >= 10000 ? `${(value/10000).toFixed(1)}` : value
        },
        splitLine: { lineStyle: { color: 'rgba(255,255,255,0.2)' } }
      },
      {
        name: '',
        // +
        nameTextStyle: { 
          color: axisTextColor, 
          fontSize, 
          fontWeight: 'bold',
          textShadowBlur: 2, // 
          textShadowColor: '#000'
        },
        nameLocation: 'middle',
        nameGap: isFull ? 100 : 50, 
        type: 'value',
        min: 0,
        max: 20000,
        axisLine: { lineStyle: { color: axisLineColor, width: 2 } }, 
        axisLabel: { 
          color: axisTextColor, 
          fontSize,
          fontWeight: 'bold', // 
          textShadowBlur: 2,  // 
          textShadowColor: '#000',
          formatter: value => value >= 10000 ? `${(value/10000).toFixed(1)}` : value
        },
        splitLine: { show: false } 
      }
    ],
    tooltip: {
      trigger: 'axis',
      backgroundColor: 'rgba(10, 30, 50, 0.9)',
      borderColor: '#1E90FF', 
      borderWidth: 2,
      textStyle: { color: '#ffffff', fontSize }, 
      formatter: params => {
        if (!params || params.length === 0) return '';
        let result = ''
        params.forEach(param => {
          if (param.seriesName === '') {
            const age = param.value?.[0] ?? '';
            const avgCost = param.value?.[1] !== undefined ? `${param.value[1].toLocaleString()}` : '';
            result += `${param.seriesName}<br/>${age}<br/>${avgCost}<br/>`;
          } else {
            const age = param.value?.[0] ?? '';
            const cost = param.value?.[1] !== undefined ? `${param.value[1].toLocaleString()}` : '';
            result += `${param.seriesName}<br/>${age}<br/>${cost}<br/>`;
          }
        })
        return result;
      }
    },
    series: [
      {
        name: '',
        type: 'scatter',
        yAxisIndex: 0, 
        symbolSize,
        data: [[12, 4539.06], [6, 4259.71], [6, 4502.23], [2, 11703.17], [3, 18338.08], [13, 1429.69], [2, 3690.94],[3, 398.37],[5, 18417.99],[15, 1813.88]],
        itemStyle: { color: '#4facfe' },
        emphasis: { symbolSize: emphasisSize, itemStyle: { shadowBlur: 10 } }
      },
      {
        name: '',
        type: 'scatter',
        yAxisIndex: 0, 
        symbolSize,
        data: [[29,13792.46],[32, 1670.55], [31, 2546.01], [32, 3533.38],[33, 2266.45],[34, 1036.73],[21, 167191.17],[22, 873.43], [34, 286.83], [20, 1532.77]],
        itemStyle: { color: '#F43F5E' },
        emphasis: { symbolSize: emphasisSize, itemStyle: { shadowBlur: 10 } }
      },
      {
        name: '',
        type: 'scatter',
        yAxisIndex: 0, 
        symbolSize,
        data: [[42, 8129.72],[45, 1805.82], [49, 1522.71],[38, 13290.86],[50, 1696.96],[49, 1281.19],[43, 2495.31],[45, 4549.96],[49, 7260.63],[50, 7273.57]],
        itemStyle: { color: '#00f2fe' },
        emphasis: { symbolSize: emphasisSize, itemStyle: { shadowBlur: 10 } }
      },
      {
        name: '',
        type: 'scatter',
        yAxisIndex: 0, 
        symbolSize,
        data:[[91, 6641.21],[85, 588.31],[66, 14986.27],[66, 1098.47],[69, 365.24],[71, 74.89], [86, 103.36],[78, 369.37],[88, 564.94],[77, 252.19]],
        itemStyle: { color: '#ff6b6b' },
        emphasis: { symbolSize: emphasisSize, itemStyle: { shadowBlur: 10 } }
      },
      {
        name: '',
        type: 'line',
        yAxisIndex: 1, 
        smooth: true, 
        symbol: 'diamond', 
        symbolSize: isFull ? 25 : 12, 
        data: averageData,
        lineStyle: { color: mainColor, width: 3.5 }, 
        itemStyle: { color: mainColor, borderColor: '#ffffff', borderWidth: 0.8 }, 
        emphasis: { 
          symbolSize: isFull ? 25 : 18, 
          itemStyle: { shadowBlur: 15, shadowColor: mainColor, opacity: 0.9 } 
        }
      }
    ]
  }
}

const initChart = () => {
  if (!chart.value) return
  myCChart = echarts.init(chart.value)
  const option = getChartOption(false) 
  myCChart.setOption(option)
  emit('register', myCChart)
  
  window.addEventListener('resize', () => {
    myCChart.resize()
    if (fullscreenCChart) fullscreenCChart.resize()
  })
}

const initFullScreenChart = () => {
  if (!fullscreenChart.value) return
  fullscreenCChart = echarts.init(fullscreenChart.value)
  const fullOption = getChartOption(true) 
  fullscreenCChart.setOption(fullOption);
  fullscreenCChart.resize();
}

const toggleFullScreen = () => {
  isFullScreen.value = !isFullScreen.value;
  if (isFullScreen.value) {
    setTimeout(() => initFullScreenChart(), 100); 
  } else {
    if (fullscreenCChart) {
      fullscreenCChart.dispose(); 
      fullscreenCChart = null;
    }
  }
}

const closeFullScreen = () => {
  isFullScreen.value = false;
  if (fullscreenCChart) {
    fullscreenCChart.dispose();
    fullscreenCChart = null;
  }
}

onMounted(() => initChart())
</script>

<style scoped>
.chart-container {
  width: 100%;
  height: 300px;
  margin-top: 5px; 
  border-radius: 6px;
  padding: 10px;
}
h2 {
  height: .6rem;
  color: #ffffff; 
  line-height: .6rem;
  font-size: 0.25rem;
  text-align: center;
  margin: 0;
  padding: 0 0 5px 0; 
}

.fullscreen-btn {
  padding: 4px 12px;
  font-size: 0.16rem;
  color: #fff;
  background: #4facfe;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.fullscreen-btn:hover {
  background: #00b8ff;
}
.fullscreen-btn.close {
  background: #ff6b6b;
  margin: 0;
}
.fullscreen-btn.close:hover {
  background: #ff4757;
}

.fullscreen-mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 10, 30, 0.98); 
  z-index: 9999;
  display: flex;
  flex-direction: column;
  padding: 20px;
  box-sizing: border-box;
}

.fullscreen-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  color: #fff;
  font-size: 30px;
}

.fullscreen-chart {
  flex: 1;
  width: 100%;
  border-radius: 6px;
}
</style>