<template>
  <div>
    <div style="display: flex; justify-content: center; align-items: center; gap: 15px; margin-bottom: 5px;">
      <h2></h2>
      <button 
        @click="toggleFullScreen" 
        style="padding: 5px 12px; border: none; border-radius: 4px; background:#4facfe; color: #fff; cursor: pointer; font-size: 12px;"
      >
        {{ isFullScreen ? '' : '' }}
      </button>
    </div>
    <div ref="chart" class="chart-container"></div>
    <div v-if="isFullScreen" class="fullscreen-modal" @click.self="closeFullScreen">
      <div class="fullscreen-header">
        <span></span>
        <button @click="closeFullScreen" style="padding: 4px 8px; border: none; background: #ff6b6b; color: #fff; border-radius: 4px; cursor: pointer;"></button>
      </div>
      <div ref="fullscreenChart" class="fullscreen-chart-container"></div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, defineEmits } from 'vue'
import * as echarts from 'echarts'

const emit = defineEmits(['register']) 
const chart = ref(null)
const fullscreenChart = ref(null)
let myCChart = null 
let fullscreenCChart = null

const isFullScreen = ref(false)

const getChartOption = (isFull = false) => {
  const fontSizeScale = isFull ? 2.5 : 1;
  const baseFontSize = 12;
  
  return {
    tooltip: {
      trigger: 'axis',
      axisPointer: { type: 'shadow' },
      backgroundColor: 'rgba(10, 30, 50, 0.9)',
      borderColor: '#1E90FF',
      borderWidth: 1,
      textStyle: { 
        color: '#fff',
        fontSize: baseFontSize * fontSizeScale 
      },
      formatter: params => { 
        let res = `<strong>${params[0].name}</strong><br/>`
        params.forEach(item => {
          res += `${item.marker} ${item.seriesName}: ${item.seriesName.includes('') ? '' : ''}${item.value.toLocaleString()}<br/>`
        })
        return res
      }
    },
    legend: {
      data: ['', ''],//, ''
      top: isFull ? 15 : 5,
      textStyle: { 
        color: '#fff',
        fontSize: baseFontSize * fontSizeScale * 0.9 
      },
      itemWidth: 12 * fontSizeScale,
      itemHeight: 12 * fontSizeScale,
      itemGap: 15 * fontSizeScale,
    },
    grid: {
      left: isFull ? '6%' : '5%',
      right: isFull ? '6%' : '8%',
      bottom: isFull ? '15%' : '9%',
      top: isFull ? '25%' : '15%',
      containLabel: true
    }, 
    xAxis: {
      type: 'category',
      data: ['', '', '', '', '', '', ''],
      axisLabel: {
        rotate: isFull ? 30 : 40,
        fontSize: baseFontSize * fontSizeScale * 1.1,
        color: '#ffffff',
        interval: 0,
        margin: 15 * fontSizeScale,
      },
      axisLine: { 
        lineStyle: { 
          color: '#444',
          width: 1 * fontSizeScale 
        } 
      },
      axisTick: { show: false },
      nameTextStyle: {
        fontSize: baseFontSize * fontSizeScale * 1.1,
        color: '#ffffff'
      }
    },
    yAxis: [
      // Y
      {
        type: 'value',
        name: '',
        nameTextStyle: { 
          color: '#4ECDC4',
          fontSize: baseFontSize * fontSizeScale * 1.1,
          padding: [0, 0, 0, 10 * fontSizeScale]
        },
        position: 'left',
        axisLabel: { 
          color: '#ffffff',
          fontSize: baseFontSize * fontSizeScale,
        },
        axisLine: { 
          lineStyle: { 
            color: '#444',
            width: 1 * fontSizeScale 
          } 
        },
        splitLine: { 
          lineStyle: { 
            color: 'rgba(255,255,255,0.1)',
            width: 0.8 * fontSizeScale 
          } 
        }
      },
      // Y
      {
        type: 'value',
        name: ' ',
        nameTextStyle: { 
          color: '#ffffff',
          fontSize: baseFontSize * fontSizeScale * 1.1,
          padding: [0, -40 * fontSizeScale, 0, 0]
        },
        position: 'right',
        axisLabel: { 
          color: '#ddd',
          fontSize: baseFontSize * fontSizeScale,
        },
        axisLine: { 
          lineStyle: { 
            color: '#444',
            width: 1 * fontSizeScale 
          } 
        },
        splitLine: { show: false }
      }
      // ""Y
    ],
    series: [
      {
        name: '',
        type: 'bar',
        yAxisIndex: 0,
        data: [8643.01, 4326.89, 2078.59, 1281.25, 1946.05, 310.29, 64.40],
        itemStyle: { color: '#4ECDC3' },
        barWidth: isFull ? 30 : 20,
        label: {
          show: isFull,
          position: 'top',
          //offset: isFull ? [0, -15 * fontSizeScale] : [0, 0], 
          color: '#4ECDC4',
          fontSize: baseFontSize * fontSizeScale * 0.8,
          fontWeight: 'bold'
        }
      },
      {
        name: '',
        type: 'bar',
        yAxisIndex: 1,
        data: [1283, 480, 3211, 1010, 383, 7232, 2114],
        itemStyle: { color: '#FF6B6B' },
        barWidth: isFull ? 30 : 20,
        label: {
          //show: isFull,
          position: 'top',
          color: '#FF6B6B',
          fontSize: baseFontSize * fontSizeScale * 0.8,
          fontWeight: 'bold'
        }
      },
      {
        name: '',
        type: 'line',
        yAxisIndex: 1, // Y
        // 
        data: [1283, 881, 3211, 1695, 691, 7232, 2114],
        itemStyle: { color: '#FFC107' }, // 
        smooth: true,
         label: {
          show: isFull,
          position: 'top',
          color: '#FFC107',
          fontSize: baseFontSize * fontSizeScale * 0.8,
          fontWeight: 'bold'
        },
        lineStyle: { 
          width: 3 * fontSizeScale 
        },
        areaStyle: {
          color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
            { offset: 0, color: 'rgba(255, 193, 7, 0.3)' },
            { offset: 1, color: 'rgba(255, 193, 7, 0)' }
          ])
        },
        symbol: 'circle',
        symbolSize: 6 * fontSizeScale,
        emphasis: {
          scale: true,
          scaleSize: 8 * fontSizeScale,
          itemStyle: { 
            shadowBlur: 15 * fontSizeScale,
            shadowColor: 'rgba(255, 193, 7, 0.8)' // 
          }
        }
      }
    ]
  }
}

const initChart = () => {
  if (!chart.value) return
  myCChart = echarts.init(chart.value)
  const option = getChartOption(false)
  myCChart.setOption(option)
  
  window.itemOneInstance = myCChart
  emit('register', myCChart)
  window.addEventListener('resize', () => {
    if (myCChart) myCChart.resize()
    if (fullscreenCChart) fullscreenCChart.resize()
  })
}

const initFullScreenChart = () => {
  if (!fullscreenChart.value) return
  fullscreenCChart = echarts.init(fullscreenChart.value)
  const option = getChartOption(true)
  fullscreenCChart.setOption(option)
  fullscreenCChart.resize()
}

const toggleFullScreen = () => {
  isFullScreen.value = !isFullScreen.value
  if (isFullScreen.value) {
    setTimeout(() => initFullScreenChart(), 100)
  } else {
    if (fullscreenCChart) {
      fullscreenCChart.dispose()
      fullscreenCChart = null
    }
  }
}

const closeFullScreen = () => {
  isFullScreen.value = false
  if (fullscreenCChart) {
    fullscreenCChart.dispose()
    fullscreenCChart = null
  }
}

onMounted(() => initChart())
</script>

<style scoped>
.chart-container {
  width: 100%;
  height: 280px; 
  margin-top: 10px;
  border-radius: 6px;
  padding: 8px;
}

h2 {
  height: .6rem;
  color: #fff;
  line-height: .6rem;
  font-size: 0.25rem;
  text-align: center;
  margin: 0;
  padding: 0 0 10px 0;
}

.fullscreen-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.95);
  z-index: 9999;
  display: flex;
  flex-direction: column;
  padding: 20px;
  box-sizing: border-box;
}

.fullscreen-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  color: #fff;
  font-size: 30px;
  font-weight: bold;
}

.fullscreen-chart-container {
  flex: 1;
  width: 100%;
  border-radius: 6px;
}
</style>