<template>
  <div>
  
    <div style="display: flex; justify-content: center; align-items: center; gap: 15px; margin-bottom: 5px;">
      <h2 class="chart-title">TOP3</h2>
     
      <button 
        class="fullscreen-btn"
        @click="toggleFullScreen"
      >
        {{ isFullScreen ? ''  : '' }}
      </button>
    </div>
    
    <div class="chart-legend">
      <div class="legend-item" v-for="(item, index) in legendData" :key="index">
        <span class="legend-color" :style="{ backgroundColor: colors[index] }"></span>
        <span class="legend-text">{{ item }}</span>
      </div>
    </div>
    
   
    <div ref="chart" class="chart-container"></div>

   
    <div v-if="isFullScreen" class="fullscreen-mask" @click.self="closeFullScreen">
      <div class="fullscreen-header">
        <span>TOP3</span>
        <button class="fullscreen-btn close" @click="closeFullScreen"></button>
      </div>

      <div class="chart-legend fullscreen-legend">
        <div class="legend-item" v-for="(item, index) in legendData" :key="index">
          <span class="legend-color" :style="{ backgroundColor: colors[index] }"></span>
          <span class="legend-text">{{ item }}</span>
        </div>
      </div>
  
      <div ref="fullscreenChart" class="fullscreen-chart"></div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, onUnmounted } from 'vue'
import * as echarts from 'echarts'

const emit = defineEmits(['register']) 
const chart = ref(null)
let myCChart = null 
let resizeChart = null 

// =====================  =====================
const isFullScreen = ref(false) // 
const fullscreenChart = ref(null) // ref
let fullscreenCChart = null // 
let fullscreenResizeHandler = null // resize
// ============================================================

const baseBlueColors = [
  '#1a365d', 
  '#2c5282', 
  '#3182ce', 
  '#63b3ed'  
];

const colors = ['#bfdbfe', '#93c5fd', '#60a5fa', '#3b82f6'];
const legendData = ['', '', '', '']

// /
const getChartOption = (isFull = false) => {
  // 
  const labelFontSize = isFull ? 16 : 12;
  const level1FontSize = isFull ? 18 : 14;
  const radius = isFull ? [0, '85%'] : [0, '80%'];
  const shadowBlur = isFull ? 12 : 8;
  const emphasisShadowBlur = isFull ? 18 : 12;

  return {
   tooltip: {
  trigger: 'item',
  formatter: ({ name, value }) => {
    const total = 11085000 + 6670000 + 2244000 + 1294000
    const rate = ((value / total) * 100).toFixed(2)
    return `${name}<br/>: ${value.toLocaleString()}<br/>: ${rate}%`
  },
  backgroundColor: 'rgba(10, 30, 50, 0.9)',
  borderColor: '#1E90FF',
  borderWidth: 1,
  textStyle: { 
    color: '#fff', 
    fontSize: isFull ? 25 : 12 
  },
  // 
  position: function (point, params, dom, rect, size) {
    // 
    const domWidth = size.contentSize[0];
    const domHeight = size.contentSize[1];
    const viewWidth = chart.value ? chart.value.offsetWidth : window.innerWidth;
    const viewHeight = chart.value ? chart.value.offsetHeight : 200;
    
    // 
    let x = point[0];
    let y = point[1];
    
    // 
    if (x + domWidth > viewWidth) {
      x = x - domWidth - 10;
    }
    
    // 
    if (y + domHeight > viewHeight) {
      y = y - domHeight - 10;
    }
    
    // 
    x = Math.max(5, x);
    y = Math.max(5, y);
    
    return [x, y];
  },
  // 
  transitionDuration: 0.3,
  // 
  confine: true,
  alwaysShowContent: false,
},
    series: {
      type: 'sunburst',
      data: [
        {
          name: '',
          children: [
            {
              name: '',
              value: 11085000,
              children: [
                { name: '', value: 5542500, children: [{ name: '', value: 2400000 }, { name: '', value: 1800000 }, { name: '', value: 1342500 }] },
                { name: '', value: 2771250, children: [{ name: '', value: 1200000 }, { name: '', value: 950000 }, { name: '', value: 621250 }] },
                { name: '', value: 2771250, children: [{ name: '', value: 1150000 }, { name: '', value: 920000 }, { name: '', value: 701250 }] }
              ]
            },
            {
              name: '',
              value: 6670000,
              children: [
                { name: '', value: 3668500, children: [{ name: '', value: 1350000 }, { name: '', value: 1150000 }, { name: '', value: 850000 }, { name: '', value: 318500 }] },
                { name: '', value: 1667500, children: [{ name: '', value: 750000 }, { name: '', value: 520000 }, { name: '', value: 397500 }] },
                { name: '', value: 1334000, children: [{ name: '', value: 850000 }, { name: '', value: 484000 }] }
              ]
            },
            {
              name: '',
              value: 2244000,
              children: [
                { name: '', value: 1234200, children: [{ name: '', value: 480000 }, { name: '', value: 420000 }, { name: '', value: 240000 }, { name: '', value: 94200 }] },
                { name: '', value: 561000, children: [{ name: '', value: 220000 }, { name: '', value: 190000 }, { name: '', value: 100000 }, { name: '', value: 51000 }] },
                { name: '', value: 448800, children: [{ name: '', value: 180000 }, { name: '', value: 140000 }, { name: '', value: 80000 }, { name: '', value: 48800 }] }
              ]
            },
            {
              name: '',
              value: 1294000,
              children: [
                { name: '', value: 711700, children: [{ name: '', value: 250000 }, { name: '', value: 210000 }, { name: '', value: 160000 }, { name: '', value: 91700 }] },
                { name: '', value: 323500, children: [{ name: '', value: 110000 }, { name: '', value: 95000 }, { name: '', value: 70000 }, { name: '', value: 48500 }] },
                { name: '', value: 258800, children: [{ name: '', value: 90000 }, { name: '', value: 75000 }, { name: '', value: 50000 }, { name: '', value: 43800 }] }
              ]
            }
          ]
        }
      ],
      radius: radius,
      sort: 'desc', 
      levels: [
        {
          itemStyle: {
            borderRadius: isFull ? 16 : 12,
            borderColor: 'rgba(255, 255, 255, 0.15)',
            borderWidth: 2,
            color: baseBlueColors[0],
            shadowColor: 'rgba(99, 179, 237, 0.3)',
            shadowBlur: shadowBlur
          }
        },
        {
          r0: '0%',
          r: '25%',
          label: {
            show: true,
            position: 'outside',//position: isFull ? 'outside' : 'inside', // 
            rotate: 0,
            fontSize: level1FontSize,
            fontWeight: 'bold',
            color: '#ffffff',
            shadowColor: 'rgba(0,0,0,0.2)',
            shadowOffsetX: 1,
            shadowOffsetY: 1
          },
          itemStyle: {
            borderWidth: 2,
            borderColor: 'rgba(255, 255, 255, 0.2)',
            borderRadius: isFull ? 12 : 8,
            color: baseBlueColors[1],
            shadowColor: 'rgba(99, 179, 237, 0.4)',
            shadowBlur: shadowBlur - 2
          }
        },
        {
          r0: '25%',
          r: '50%',
          label: {
            show: true,
            rotate: 'radial',
            fontSize: labelFontSize,
            color: '#ffffff',
            shadowColor: 'rgba(0,0,0,0.15)',
            shadowOffsetX: 1,
            shadowOffsetY: 1
          },
          itemStyle: {
            borderWidth: 1,
            borderColor: 'rgba(255, 255, 255, 0.15)',
            borderRadius: isFull ? 8 : 4,
            color: baseBlueColors[2],
            shadowColor: 'rgba(99, 179, 237, 0.3)',
            shadowBlur: shadowBlur - 4
          }
        },
        {
          r0: '50%',
          r: '75%',
          label: { show: false },
          itemStyle: {
            borderWidth: 1,
            borderColor: 'rgba(255, 255, 255, 0.1)',
            borderRadius: isFull ? 4 : 2,
            color: baseBlueColors[3],
            shadowColor: 'rgba(99, 179, 237, 0.2)',
            shadowBlur: shadowBlur - 6
          }
        }
      ],
      label: {
        minAngle: 15, 
        overflow: 'truncate',
        width: isFull ? 100 : 70
      },
      emphasis: {
        focus: 'ancestor',
        label: {
          show: true,
          fontWeight: 'bold',
          color: '#ffffff',
          fontSize: isFull ? 20 : 14
        },
        itemStyle: {
          borderRadius: isFull ? 16 : 10,
          brightness: 0.15,
          shadowBlur: emphasisShadowBlur,
          shadowColor: 'rgba(99, 179, 237, 0.6)'
        }
      },
      itemStyle: {
        color: (params) => {
          const level1Node = params.data.path?.[1];
          if (!level1Node) return baseBlueColors[0]; 
          const level1Name = level1Node.name;
          const index = legendData.findIndex(item => item === level1Name);
          return index > -1 ? colors[index] : baseBlueColors[Math.min(params.level, 3)];
        }
      }
    }
  }
}

const initChart = () => {
  if (!chart.value) return
  myCChart = echarts.init(chart.value)

  // 
  const option = getChartOption(false)
  myCChart.setOption(option)
  emit('register', myCChart)

  resizeChart = () => {
    if (myCChart) myCChart.resize()
    // resize
    if (fullscreenCChart) fullscreenCChart.resize()
  }
  window.addEventListener('resize', resizeChart)
}

// =====================  =====================
// 
const initFullScreenChart = () => {
  if (!fullscreenChart.value) return
  fullscreenCChart = echarts.init(fullscreenChart.value)
  
  // 
  const option = getChartOption(true)
  fullscreenCChart.setOption(option)
  fullscreenCChart.resize()

  // resize
  fullscreenResizeHandler = () => {
    if (fullscreenCChart) fullscreenCChart.resize()
  }
  window.addEventListener('resize', fullscreenResizeHandler)
}

// /
const toggleFullScreen = () => {
  isFullScreen.value = !isFullScreen.value;
  if (isFullScreen.value) {
    setTimeout(() => initFullScreenChart(), 100); // DOM
  } else {
    closeFullScreen()
  }
}

// 
const closeFullScreen = () => {
  isFullScreen.value = false;
  if (fullscreenCChart) {
    fullscreenCChart.dispose(); // 
    fullscreenCChart = null;
  }
  if (fullscreenResizeHandler) {
    window.removeEventListener('resize', fullscreenResizeHandler);
    fullscreenResizeHandler = null;
  }
}
// ============================================================

onMounted(() => {
  initChart()
})

onUnmounted(() => {
  if (resizeChart) window.removeEventListener('resize', resizeChart)
  if (myCChart) myCChart.dispose()
  myCChart = null
  // 
  closeFullScreen()
})
</script>

<style scoped>
/*  */
.chart-container {
  width: 100%;
  height: 200px;
  margin-top: 0px; 
  margin-bottom: 5px; 
  border-radius: 8px;
  overflow: hidden;
  position: relative;
  top: -5px; 
  background: linear-gradient(135deg, rgba(10, 30, 60, 0.1), rgba(15, 50, 90, 0.05));
}

.chart-legend {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin: 5px 0; 
  padding: 8px 12px;
  background: rgba(26, 54, 93, 0.15); 
  border-radius: 6px;
  border: 1px solid rgba(99, 179, 237, 0.2);
}

.legend-item {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 0.18rem;
  color: #e0f2fe;
  font-family: 'Microsoft YaHei', sans-serif;
}

.legend-color {
  width: 12px;
  height: 12px;
  border-radius: 4px;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.legend-text {
  white-space: nowrap;
}

.chart-title {
  height: 0.6rem;
  color: #e0f2fe;
  line-height: 0.6rem;
  font-size: 0.25rem;
  text-align: center;
  margin-bottom: 0px; 
  margin-top: 0;
  font-weight: 600;
  text-shadow: 0 0 8px rgba(99, 179, 237, 0.3);
}

/* =====================  ===================== */
/*  */
.fullscreen-btn {
  padding: 4px 12px;
  font-size: 0.16rem;
  color: #fff;
  background: #4facfe;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.fullscreen-btn:hover {
  background: #00b8ff;
}
.fullscreen-btn.close {
  background: #ff6b6b;
  margin: 0;
}
.fullscreen-btn.close:hover {
  background: #ff4757;
}

/*  */
.fullscreen-mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 10, 30, 0.98);
  z-index: 9999;
  display: flex;
  flex-direction: column;
  padding: 20px;
  box-sizing: border-box;
}

/*  */
.fullscreen-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  color: #e0f2fe;
  font-size: 30px;
  font-weight: 600;
  text-shadow: 0 0 8px rgba(99, 179, 237, 0.5);
}

/*  */
.fullscreen-legend {
  margin: 10px 0;
  padding: 12px 20px;
  gap: 20px;
}
.fullscreen-legend .legend-item {
  font-size: 0.4rem;
}
.fullscreen-legend .legend-color {
  width: 16px;
  height: 16px;
}

/*  */
.fullscreen-chart {
  flex: 1;
  width: 100%;
  border-radius: 8px;
  background: linear-gradient(135deg, rgba(10, 30, 60, 0.2), rgba(15, 50, 90, 0.1));
}
/* ============================================================ */
</style>