<template>
  <div class="mapshandonglinyishi" id="mapshandonglinyishi">
  
    <div class="custom-title-area">

      <div class="gender-card male-card title-left">
        <div class="gender-icon"></div>
        <div class="gender-info">
          <div class="gender-label"></div>
          <div class="gender-count" id="maleCount">8,503</div>
          <div class="gender-percent">51.7%</div>
        </div>
      </div>
      
 
      <div class="title-text"></div>
      

      <div class="gender-card female-card title-right">
        <div class="gender-icon"></div>
        <div class="gender-info">
          <div class="gender-label"></div>
          <div class="gender-count" id="femaleCount">7,952</div>
          <div class="gender-percent">48.3%</div>
        </div>
      </div>
    </div>
    
    <div class="chart-container">
      <div class="chart" id="chartContainer"></div>
    </div>
  </div>
</template>

<script setup>
import axios from "axios";
import { onMounted, reactive, inject, defineEmits, ref } from "vue";

const emit = defineEmits(['register']);  
const $echarts = inject("echarts");
const mapData = reactive({});

// 
const totalPatients = 16455;
const maleCount = ref(8503);
const femaleCount = ref(7952);
const malePercentage = ref(51.7);
const femalePercentage = ref(48.3);

let myCharts = null;

// ECharts
const baseChartOption = {
  geo: {
    map: "shandonglinyishi",
    left: "10%", 
    top: "10%",  
    right: "10%",
    bottom: "10%",
    itemStyle: {
      areaColor: "#cce7ff", 
      borderColor: "#2f80ed", 
      borderWidth: 1.5, 
      shadowColor: "rgba(47, 128, 237, 0.2)", 
      shadowBlur: 15, 
      emphasis: {
        focus: "self",
        areaColor: "#b3d8fc", 
        borderColor: "#1d71d8"
      }
    }
  },
  title: {
    show: false
  },
  tooltip: {
    trigger: "item",
    backgroundColor: "rgba(255, 255, 255, 0.9)", 
    borderColor: "#e6e6e6",
    borderWidth: 1,
    padding: [10, 15],
    textStyle: { color: "#1d2129", fontSize: 12 },
    formatter: (params) => {
      const peopleCount = params.value?.[2] || 0;
      return `<div style="text-align: left;">
        <div style="font-weight: bold; color: #d92d20; margin-bottom: 4px;">${params.name}</div>
        <div style="color: #4e5969;"><span style="font-size: 14px; color: #1d2129;">${peopleCount.toLocaleString()}</span> </div>
      </div>`;
    }
  },
  visualMap: {
    type: "continuous",
    min: 100,
    max: 6000,
    calculable: true,
    left: "5%", 
    bottom: "5%", 
    itemWidth: 15, 
    itemHeight: 120, 
    inRange: { 
      color: ["#e8f4fd", "#b7eb8f", "#ffd591"] 
    },
    textStyle: {
      color: "#ffffff", 
      fontSize: 11
    },
    text: ["", ""], 
    textGap: 10
  },
  series: [
    {
      type: "scatter",
      coordinateSystem: "geo",
      symbolSize: (value) => { 
        return Math.sqrt(value[2]) / 5 + 5;
      },
      itemStyle: {
        color: "#0d3c7a",
        opacity: 0.8,
        emphasis: {
          color: "#d92d20", 
          shadowBlur: 10,
          shadowColor: "rgba(245, 63, 63, 0.3)"
        }
      },
      data: [],
      animationDuration: 1500,  
      animationEasing: 'elasticOut',
      animationDelay: (idx) => idx * 100
    }
  ]
};

// 
const initialData = [
  { name: "", value: [118.35, 35.05, 5751] },
  { name: "", value: [118.46, 34.90, 2517] },
  { name: "", value: [117.63, 35.49, 1789] },
  { name: "", value: [118.83, 35.17, 722] },
  { name: "", value: [118.33, 35.06, 638] },
  { name: "", value: [118.73, 34.87, 521] },
  { name: "", value: [118.64, 35.79, 455] },
  { name: "", value: [118.36, 34.61, 367] },
  { name: "", value: [118.03, 34.84, 347] },
  { name: "", value: [117.97, 35.26, 284] },
  { name: "", value: [118.47, 35.54, 258] },
  { name: "", value: [117.63, 35.49, 117] },
  { name: "", value: [117.95, 35.70, 110] }
];

async function getState() {
  try {
    const response = await axios.get("http://localhost:8080/map/shandonglinyishi.geojson");
    Object.assign(mapData, response.data);
  } catch (error) {
    console.error(":", error);
  }
}

// 
function updateGenderData() {
  const maleBase = 8503;
  const femaleBase = 7952;
  
  maleCount.value = Math.floor(maleBase * (0.98 + Math.random() * 0.04));
  femaleCount.value = Math.floor(femaleBase * (0.98 + Math.random() * 0.04));
  
  const maleElement = document.getElementById('maleCount');
  const femaleElement = document.getElementById('femaleCount');
  
  if (maleElement && femaleElement) {
    const total = maleCount.value + femaleCount.value;
    const malePercentage = ((maleCount.value / total) * 100).toFixed(1);
    const femalePercentage = ((femaleCount.value / total) * 100).toFixed(1);
    
    maleElement.textContent = maleCount.value.toLocaleString();
    femaleElement.textContent = femaleCount.value.toLocaleString();
    
    const malePercent = document.querySelector('.male-card .gender-percent');
    const femalePercent = document.querySelector('.female-card .gender-percent');
    if (malePercent) malePercent.textContent = `${malePercentage}%`;
    if (femalePercent) femalePercent.textContent = `${femalePercentage}%`;
    
    maleElement.style.transform = 'scale(1.1)';
    femaleElement.style.transform = 'scale(1.1)';
    setTimeout(() => {
      maleElement.style.transform = 'scale(1)';
      femaleElement.style.transform = 'scale(1)';
    }, 300);
  }
}

onMounted(async () => {
  await getState();
  $echarts.registerMap("shandonglinyishi", mapData);
  
  myCharts = $echarts.init(document.getElementById("chartContainer"));
  
  const initialOption = {
    ...baseChartOption,
    series: [{
      ...baseChartOption.series[0],
      data: initialData
    }]
  };
  
  myCharts.setOption(initialOption);

  let updateInterval;
  let genderUpdateInterval;
  
  if (typeof window !== 'undefined') {
    updateInterval = setInterval(() => {
      const updatedData = initialData.map(item => ({
        ...item,
        value: [
          item.value[0],
          item.value[1],
          Math.round(item.value[2] * (0.95 + Math.random() * 0.1))
        ]
      }));
      
      myCharts.setOption({
        series: [{
          data: updatedData,
          animationDuration: 800,
          animationEasingUpdate: 'cubicOut'
        }]
      });
    }, 5000);

    genderUpdateInterval = setInterval(updateGenderData, 5000);
  }

  myCharts.on('click', (params) => {
    console.log('', {
      : params.name,
      : params.value?.[2] || '',
      : params.value?.slice(0, 2) || []
    });
  });

  emit('register', myCharts);  

  const handleResize = () => {
    if (myCharts) {
      myCharts.resize();
    }
  };
  
  window.addEventListener("resize", handleResize);
  
  return () => {
    if (updateInterval) clearInterval(updateInterval);
    if (genderUpdateInterval) clearInterval(genderUpdateInterval);
    window.removeEventListener("resize", handleResize);
    if (myCharts) {
      myCharts.dispose();
    }
  };
});
</script>

<style scoped>
.mapshandonglinyishi {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

/*  -  */
.custom-title-area {
  margin: 0 0 15px 0;
  color: #ffffff;
  font-size: 16px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 90px; /*  */
  padding: 0 15px; /*  */
  background: linear-gradient(135deg, rgba(29, 113, 216, 0.9), rgba(47, 128, 237, 0.7));
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  position: relative;
  z-index: 10;
  min-height: 90px; /*  */
}

.title-text {
  font-size: 20px; /*  */
  font-weight: bold;
  color: white;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.4);
  flex: 1;
  text-align: center;
  padding: 0 25px; /*  */
  white-space: nowrap; /*  */
  letter-spacing: 0.5px; /*  */
}

/*  */
.chart-container {
  flex: 1;
  width: 100%;
  position: relative;
  min-height: 0; /*  */
}

.chart {
  width: 100%;
  height: 100%;
}

/*  -  */
.gender-card {
  background: rgba(255, 255, 255, 0.97);
  border-radius: 12px;
  padding: 12px 18px; /*  */
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(8px);
  border: 1px solid rgba(255, 255, 255, 0.4);
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 12px; /*  */
  width: 220px; /*  */
  height: 72px; /*  */
  flex-shrink: 0; /*  */
}

.gender-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.25);
}

.male-card {
  border-left: 5px solid #3498db;
}

.female-card {
  border-left: 5px solid #e91e63;
}

.title-left {
  order: 1;
}

.title-right {
  order: 3;
}

.gender-icon {
  font-size: 28px; /*  */
  width: 52px; /*  */
  height: 52px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  flex-shrink: 0;
}

.male-card .gender-icon {
  background: linear-gradient(135deg, #e3f2fd, #90caf9);
  color: #1565c0;
  box-shadow: 0 3px 10px rgba(21, 101, 192, 0.3);
}

.female-card .gender-icon {
  background: linear-gradient(135deg, #fce4ec, #f48fb1);
  color: #ad1457;
  box-shadow: 0 3px 10px rgba(173, 20, 87, 0.3);
}

.gender-info {
  flex: 1;
  min-width: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.gender-label {
  font-size: 13px; /*  */
  color: #555;
  margin-bottom: 4px;
  font-weight: 600;
  white-space: nowrap;
  overflow: visible; /*  */
  text-overflow: clip;
}

.gender-count {
  font-size: 24px; /*  */
  font-weight: 800; /*  */
  margin-bottom: 3px;
  transition: all 0.3s ease;
  white-space: nowrap;
  overflow: visible;
  text-overflow: clip;
  line-height: 1.2;
  letter-spacing: 0.5px; /*  */
}

.male-card .gender-count {
  color: #1565c0;
  text-shadow: 0 1px 2px rgba(21, 101, 192, 0.2);
}

.female-card .gender-count {
  color: #ad1457;
  text-shadow: 0 1px 2px rgba(173, 20, 87, 0.2);
}

.gender-percent {
  font-size: 13px; /*  */
  color: #fff;
  background: rgba(0, 0, 0, 0.15);
  padding: 4px 10px; /*  */
  border-radius: 12px;
  display: inline-block;
  white-space: nowrap;
  font-weight: 600;
  line-height: 1.2;
  align-self: flex-start; /*  */
}

/*  */
@media (max-width: 1200px) {
  .custom-title-area {
    height: 80px;
    padding: 0 10px;
  }
  
  .gender-card {
    width: 200px;
    height: 66px;
    padding: 10px 15px;
  }
  
  .gender-count {
    font-size: 22px;
  }
  
  .title-text {
    font-size: 18px;
    padding: 0 15px;
  }
}

@media (max-width: 992px) {
  .custom-title-area {
    height: 75px;
    flex-wrap: nowrap;
  }
  
  .gender-card {
    width: 180px;
    height: 62px;
    padding: 8px 12px;
  }
  
  .gender-count {
    font-size: 20px;
  }
  
  .gender-label {
    font-size: 12px;
  }
  
  .title-text {
    font-size: 16px;
    padding: 0 10px;
  }
}
</style>